import { useLocation } from "react-router-dom"
import { db } from "../database/db";
import { toast } from 'react-toastify';
import { useLiveQuery } from "dexie-react-hooks";
import "../Css/rightbar.css";

function AnnotationTextTable( { data, setDetails }) {
    const { pathname } = useLocation();
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
     // Grouping areas by name
     const storedTexts = data?.storedTexts || [];
     const groupedRectangleData = {};

     storedTexts.forEach((item, index) => {
        const name = item.meta.name;
        groupedRectangleData[`${name}-${index}`] = {
            name: item.meta.name,
            data: item.data,
        };
    });
        let previousRowIndex = null;
        const handleMouseInOut = async(e, index, isEntering) => {
            let newText = [...Data?.storedTexts];
            if (newText[index]) {
                 // Check if the record exists in the database
                const existingRecord = await db.drawings
                    .where({ path: pathname })
                    .first();
                    if (existingRecord) {
                        if (isEntering) {
                            existingRecord.storedTexts[index].meta.fill = '#f5ed05';
                            if(previousRowIndex){
                                existingRecord.storedTexts[previousRowIndex-1].meta.fill = '#E914B7';
                            }
                        } 
                        if (!isEntering) {
                            existingRecord.storedTexts[index].meta.fill = '#E914B7';
                            previousRowIndex = index+1;
                        }
                        // Update the record in the database
                        await db.drawings
                            .where({ path: pathname })
                            .modify({ storedTexts: existingRecord.storedTexts });
                        }
                    }
                };
                
                
                const handleDelete =async (index) => {
                    let newStoredText = data?.storedTexts.filter((item, i) => i !== index);
                    
                    await db.drawings
                    .where({ path: pathname })
                    .modify({ storedTexts: newStoredText });
                toast.success('Text Deleted Successfully!', {...toastOptions});
        };
        const toastOptions = {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: 0,
        };
    return (
       <>
        <table>
            <tbody>
                {groupedRectangleData &&
                    Object.keys(groupedRectangleData).map((lineKey, index) => {
                        const line = groupedRectangleData[lineKey];
                        return (
                            <tr
                                key={index}
                            >
                            <div
                                onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                            >
                                <td className='w-15'>{index+1}</td>
                                <td>{line.name}</td>
                                <td>
                                    <i 
                                        class="fa fa-trash trash-icon" 
                                        aria-hidden="true" 
                                        onClick={() => handleDelete(index)}
                                    ></i>
                                </td>
                            </div>
                            </tr>
                        );
                    })}
            </tbody>
        </table>
       </>
    )
}

export default AnnotationTextTable;