import { useReducer } from "react";
const screenState = {
    state: 'min',
    icon: <i className="fa-light fa-arrows-maximize"></i>
}
function reducer(state, action) {
    switch (action) {
        case 'min':
            return {
                state: 'max',
                icon: <i className="fa-regular fa-minimize"></i>
            };
        case 'max':
            return {
                state: 'min',
                icon: <i className="fa-light fa-arrows-maximize"></i>
            };
        default:
            return state;
    }
}
function ScreenToggle() {
    const [screen, dispatch] = useReducer(reducer, screenState)
    const toggleScreen = () => {
        let elem = document.documentElement;
        if (screen.state === 'min') {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
            } else if (elem.webkitRequestFullscreen) { /* Safari */
                elem.webkitRequestFullscreen();
            } else if (elem.msRequestFullscreen) { /* IE11 */
                elem.msRequestFullscreen();
            }
            dispatch('min')
        }
        else if (screen.state === 'max') {
            if (document.exitFullscreen) {
                document.exitFullscreen();
            } else if (document.webkitExitFullscreen) { /* Safari */
                document.webkitExitFullscreen();
            } else if (document.msExitFullscreen) { /* IE11 */
                document.msExitFullscreen();
            }
            dispatch('max')
        }
    }
    return (
        <li className="dropdown maximize_window d-right text-white" tabIndex="0" onClick={toggleScreen}>
            {screen.icon}
        </li>
    )
}
export default ScreenToggle;