import { routes, projectId } from "../Constants";
import { toast } from "react-toastify";
import { db } from "../database/db";
import useSignal from "../hooks/signals";
function DeleteAll() {
  const signal = useSignal();

  const deleteAll = async () => {
    let url = routes.deleteAll + projectId;
    let response = await fetch(url, {
      method: "GET",
      headers: {
        Authorization: `Bearer ${routes.__token}`,
      },
    });
    const databaseName = db.name;
    const databaseVersion = db.verno;
    const tables = [];

    let data = await response.json();
    let projectPath = data.project_path;
    const splitName = projectPath.split("/");
    const uploadFolder = splitName[0];
    const projectFolder = splitName[1];
    const directoryFolder = splitName[2];
    const pathName =
      "/" + uploadFolder + "/" + projectFolder + "/" + directoryFolder;

    if (pathName) {
      const dbProject = await db.drawings
        .where("path")
        .startsWith(pathName)
        .toArray();
      if (dbProject.length > 0) {
        await Promise.all(
          dbProject.map(async (record) => {
            await db.drawings.delete(record.id);
          })
        );
      }
    }
    const drawings = await db.drawings.toArray();
    tables.push({
      name: "drawings",
      rowCount: drawings.length,
      //   data: drawings,
      data: drawings.map((drawing) => ({
        area: drawing.area,
        counts: drawing.counts,
        factor: drawing.factor,
        id: drawing.id,
        lines: drawing.lines,
        path: drawing.path,
        perimeter: drawing.perimeter,
        scale: drawing.scale,
        storedEllipses: drawing.storedEllipses,
        storedHyperLinks: drawing.storedHyperLinks,
        storedLines: drawing.storedLines,
        storedRectangles: drawing.storedRectangles,
        storedTexts: drawing.storedTexts,
      })),
    });
    const exportData = {
      data: {
        databaseName,
        databaseVersion,
        tables,
      },
    };
    // saveDataToBackend(exportData);
    if (data.status === 200) {
      toast.success("All Files Deleted!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
      signal.emit("refresh");
    }
    if (data.status === 500) {
      toast.warning("No files to deleted!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }
  };
  const saveDataToBackend = async (data) => {
    try {
      const response = await fetch(`/api/sync_local_db`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${routes.__token}`,
        },
        body: JSON.stringify(data),
      });
      if (!response.ok) {
        throw new Error("Error generating proposal");
      }
      console.log("Database Synchronized");
      // console.log('check result',responseData);
    } catch (error) {
      console.error("Error saving data to the Laravel backend:", error);
    }
  };
  return (
    <>
      <li>
        <label className="custom-label" onClick={deleteAll}>
          Delete All
        </label>
      </li>
    </>
  );
}

export default DeleteAll;
