import { useLocation } from "react-router-dom"
import { db } from "../database/db";
import { toast } from 'react-toastify';
import { useLiveQuery } from "dexie-react-hooks";
import "../Css/rightbar.css";

function AnnotationHyperlinkTable( { data, setDetails }) {
    const { pathname } = useLocation();
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
     // Grouping areas by name
    //  console.log('data', data);
     const storedHyperLinks = data?.storedHyperLinks || [];
     const groupedRectangleData = {};

     storedHyperLinks.forEach((item, index) => {
        const name = item.meta.name;
        groupedRectangleData[`${name}-${index}`] = {
            name: item.meta.name,
            data: item.data,
        };
    });
        let previousRowIndex = null;
        const handleMouseInOut = async(e, index, isEntering) => {
            let newHyperlink = [...Data?.storedHyperLinks];
            if (newHyperlink[index]) {
                 // Check if the record exists in the database
                const existingRecord = await db.drawings
                    .where({ path: pathname })
                    .first();
                    if (existingRecord) {
                        if (isEntering) {
                            existingRecord.storedHyperLinks[index].meta.fill = '#f5ed05';
                            existingRecord.storedHyperLinks[index].meta.stroke = '#f5ed05';
                            if(previousRowIndex){
                                existingRecord.storedHyperLinks[previousRowIndex-1].meta.fill = 'blue';
                                existingRecord.storedHyperLinks[previousRowIndex-1].meta.stroke = 'blue';
                            }
                        } 
                        if (!isEntering) {
                            existingRecord.storedHyperLinks[index].meta.fill = 'blue';
                            existingRecord.storedHyperLinks[index].meta.stroke = 'blue';
                            previousRowIndex = index+1;
                        }
                        // Update the record in the database
                        await db.drawings
                            .where({ path: pathname })
                            .modify({ storedHyperLinks: existingRecord.storedHyperLinks });
                    }
            }
         };

         const handleDelete =async (index) => {
            let newStoredHyperlink = data?.storedHyperLinks.filter((item, i) => i !== index);
            await db.drawings
            .where({ path: pathname })
            .modify({ storedHyperLinks: newStoredHyperlink });
            //for deleting related record
            let relatedHyperlink = data?.storedHyperLinks.filter((item, i) => i === index);
            let getHyperlink = relatedHyperlink[0].data.hyperlink;
            let hyperlinkPath = relatedHyperlink[0].path;
            const projectPath = hyperlinkPath.split('/');
            const path = projectPath[2]+'/'+projectPath[3];
            let relaedHyperlinkPath = '/projects/'+getHyperlink;
            let hyperlinkData = await db.drawings.where({
                path: relaedHyperlinkPath
            }).first();
            // Update storedHyperlinks based on the condition
            let relatedIndex = hyperlinkData?.storedHyperLinks.findIndex(item => item.data.hyperlink === path);
            let newRelatedStoredHyperlink = hyperlinkData?.storedHyperLinks.filter((item, i) => i !== relatedIndex);
             await db.drawings
            .where({ path: relaedHyperlinkPath })
            .modify({ storedHyperLinks: newRelatedStoredHyperlink });
            toast.success('Hyperlink Deleted Successfully!', {...toastOptions});
        };
        const toastOptions = {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: 0,
          };
    return (
       <>
        <table>
            <tbody>
                {groupedRectangleData &&
                    Object.keys(groupedRectangleData).map((lineKey, index) => {
                        const line = groupedRectangleData[lineKey];
                        return (
                            <tr
                                key={index}
                            >
                            <div
                                onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                            >
                                <td className='w-15'>{index+1}</td>
                                <td>{line.name}</td>
                                <td>
                                    <i 
                                        class="fa fa-trash trash-icon" 
                                        aria-hidden="true" 
                                        onClick={() => handleDelete(index)}
                                    ></i>
                                </td>
                            </div>
                            </tr>
                        );
                    })}
            </tbody>
        </table>
       </>
    )
}

export default AnnotationHyperlinkTable;