import { useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes } from "../Constants";
import { toast} from "react-toastify";
import "../proposalpopup.css";

function CreateProjectPopup({ setDisplay }) {
    const [projectData, setProjectData] = useState({
      name: "",
      bid_date: "",
    });
    const handleFormSubmit = async(event) => {
      event.preventDefault();
        try {
          let response = await fetch(routes.newProject, {
            method: "POST",
            headers: {
              "Authorization": `Bearer ${routes.__token}`,
              "Content-Type": "application/json",
            },
            body: JSON.stringify(projectData),
        })
          if (response.status === 200) {
            const responseData = await response.json();
            toast.success('Data Saved Successfully!', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: 0,
          });
          window.location.href = responseData.url;
          } else {
            toast.error('Something went wrong. Please, try again!', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: 0,
          }); 
              console.error("Error saving project:", response);
          }
      } catch (error) {
          console.error("Error saving project:", error);
          // Handle any error actions here
      }
    };
   
    return (
        createPortal(
            <Draggable>
                <div className="popup__create__project">
                    <div className="modal-header">
                    Create New Project
                    <span className="close-btn" onClick={() => { setDisplay(false); }}>&times;</span>
                    </div>
                  <form className="modal-body" 
                  onSubmit={handleFormSubmit}
                  >
                        <h3 className="content-heading">
                            Create New Project
                        </h3>
                        <div className="project-content">
                            <label className="label">Project Name</label>
                              <input 
                                  type="text"
                                  name="name"
                                  value={projectData.name}
                                  onChange={(e) => setProjectData({ ...projectData, name: e.target.value })}
                                  required
                              />
                          </div>
                          <div className="project-content">
                                <label className="label">BID Date</label>
                                    <input 
                                        type="date"
                                        name="bid_date"
                                        value={projectData.bid_date}
                                        onChange={(e) => setProjectData({ ...projectData, bid_date: e.target.value })}
                                        required
                                    />
                            </div>
                            <div className="project-content project-btn-content">
                            <button className="open-project-btn-project" type="submit">Save Project</button>
                            </div>
                    </form>
                </div>
            </Draggable>, document.querySelector('#root'))
    )
}

export default CreateProjectPopup;