import { useLocation } from "react-router-dom"
import { db } from "../database/db";
import { toast } from 'react-toastify';
import { useLiveQuery } from "dexie-react-hooks";
import "../Css/rightbar.css";

function AnnotationLineTable( { data, setDetails }) {
    const { pathname } = useLocation();
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
     // Grouping areas by name
     const storedLines = data?.storedLines || [];
     const groupedLinesData = {};

     storedLines.forEach((item, index) => {
        const name = item.meta.name;
        groupedLinesData[`${name}-${index}`] = {
            name: item.meta.name,
            data: item.data,
        };
    });
        let previousRowIndex = null;
        const handleMouseInOut = async(e, index, isEntering) => {
            
            let newLines = [...Data?.storedLines];
            if (newLines[index]) {
                 // Check if the record exists in the database
                const existingRecord = await db.drawings
                    .where({ path: pathname })
                    .first();
                    // console.log('check existing record', existingRecord);
                    if (existingRecord) {
                        if (isEntering) {
                            existingRecord.storedLines[index].meta.stroke = '#f5ed05';
                            if(previousRowIndex){
                                existingRecord.storedLines[previousRowIndex-1].meta.stroke = 'red';
                            }
                        } 
                        if (!isEntering) {
                            existingRecord.storedLines[index].meta.stroke = 'red';
                            previousRowIndex = index+1;
                        }
                        // Update the record in the database
                        await db.drawings
                            .where({ path: pathname })
                            .modify({ storedLines: existingRecord.storedLines });
                    }
            }
         };

         const handleDelete =async (index) => {
            let newStoredLines = data?.storedLines.filter((item, i) => i !== index);

            await db.drawings
            .where({ path: pathname })
            .modify({ storedLines: newStoredLines });
            toast.success('Line Deleted Successfully!', {...toastOptions});
        };
        const toastOptions = {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: 0,
          };

    return (
       <>
        <table>
            <tbody>
                {groupedLinesData &&
                    Object.keys(groupedLinesData).map((lineKey, index) => {
                        const line = groupedLinesData[lineKey];
                        return (
                            <tr
                                key={index}
                            >
                            <div
                                onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                            >
                                <td className='w-15'>{index+1}</td>
                                <td>{line.name}</td>
                                <td>
                                    <i 
                                        class="fa fa-trash trash-icon" 
                                        aria-hidden="true" 
                                        onClick={() => handleDelete(index)}
                                    ></i>
                                </td>
                            </div>
                            </tr>
                        );
                    })}
            </tbody>
        </table>
       </>
    )
}

export default AnnotationLineTable;