import { useEffect, useState } from 'react';
import CountRowContext from './CountRowContext';
import { useLocation } from "react-router-dom"
import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";

function CountTable({ data, setDetails }) {
    const [context, setContext] = useState(null);
    const { pathname } = useLocation();
    const handleContext = (e, index) => {
        e.preventDefault();
        setDetails(null)
        let { clientX, clientY } = e;
        setContext({ x: clientX, y: clientY, index })
    }
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
    useEffect(() => {
        let contextEvent = () => {
            setContext(null)
        }
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent)
        }
    }, [])
    // Group counts by meta name
    const allCounts = data?.counts || [];
    const groupedCountData = {};

    allCounts.forEach((item) => {
        const name = item.meta.name;

        if (groupedCountData[name]) {
            groupedCountData[name].push({
                name: item.meta.name,
                data: item.data,
                value1: item.data[item.data.length - 1][1],
                value2: item.data[item.data.length - 1][2],
            });
        } else {
            groupedCountData[name] = [
                {
                    name: item.meta.name,
                    data: item.data,
                    value1: item.data[item.data.length - 1][1],
                    value2: item.data[item.data.length - 1][2],
                },
            ];
        }
    });
    // Calculate the sum of value1 for each group of counts
    Object.keys(groupedCountData).forEach((groupName) => {
        const sumValue1 = groupedCountData[groupName].reduce(
            (sum, entry) => sum + entry.value1,
            0
        );
        groupedCountData[groupName].sumValue1 = sumValue1;

        // Merge the data arrays from the grouped counts into a single data array within the group
        const mergedDataArray = groupedCountData[groupName].reduce((mergedData, entry) => {
            return mergedData.concat(entry.data);
        }, []);

        groupedCountData[groupName].data = mergedDataArray;
    });

    let previousRowIndex = null;
    const handleMouseInOut = async(e, index, isEntering) => {
        
        let newCounts = [...Data?.counts];
        if (newCounts[index]) {
        //     // Check if the record exists in the database
            const existingRecord = await db.drawings
                .where({ path: pathname })
                .first();
                // console.log('check existing record', existingRecord);
                if (existingRecord) {
                    if (isEntering) {
                        existingRecord.counts[index].meta.stroke = '#f5ed05';
                        existingRecord.counts[index].meta.fill = '#f5ed05';
                        if(previousRowIndex){
                            existingRecord.counts[previousRowIndex-1].meta.stroke = '#000000';
                            existingRecord.counts[previousRowIndex-1].meta.fill = 'red';
                        }
                    } 
                    if (!isEntering) {
                        existingRecord.counts[index].meta.stroke = '#000000';
                        existingRecord.counts[index].meta.fill = 'red';
                        previousRowIndex = index+1;
                    }
                    // Update the record in the database
                    await db.drawings
                        .where({ path: pathname })
                        .modify({ counts: existingRecord.counts });
                }
        }
     };
    // console.log('count group', groupedCountData);
    return (
        <>
            <table>
                 <tbody>
                {groupedCountData &&
                    Object.keys(groupedCountData).map((groupName, index) => {
                        return (
                            <tr
                                key={index}
                                onContextMenu={(e) => handleContext(e, index)}
                                onClick={() =>
                                    setDetails({
                                        name: groupName,
                                        data: groupedCountData[groupName].data,
                                    })
                                }
                            >
                            <div
                                onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                            >
                                <td className='w-15'>
                                    <span className="count-preview"></span>
                                </td>
                                <td>{groupName}</td>
                                <td>{groupedCountData[groupName].sumValue1}</td>
                                <td>{groupedCountData[groupName][0].value2}</td>
                            </div>
                            </tr>
                        );
                    })}
            </tbody>
            </table>
            {context ? <CountRowContext details={context} /> : <></>}
        </>
    )
}

export default CountTable;