import { useNavigate } from "react-router-dom";
import { routes } from "../Constants";
import useSignal from "../hooks/signals";
import { db } from "../database/db";
function FileContext({ details, setActive }) {
  const navigate = useNavigate();
  const signal = useSignal();
  const openFile = () => {
    navigate(`/${details.plan}`);
  };

  const downloadFile = () => {
    let download = routes.public + "/" + details.plan;
    let anchor = document.createElement("a");
    anchor.href = download;
    anchor.download = "document.pdf";
    anchor.target = "_blank";
    anchor.click();
  };
  const deleteFile = async () => {
    let formData = new FormData();
    formData.append("path", details.plan);
    let response = await fetch(routes.delete, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${routes.__token}`,
      },
      body: formData,
    });
    const databaseName = db.name;
    const databaseVersion = db.verno;
    const tables = [];

    const filePath = details.plan;
    const splitName = filePath.split("/");
    const uploadFolder = splitName[0];
    const projectFolder = splitName[1];
    const directoryFolder = splitName[2];
    const fileName = splitName[3];
    const fileNameEncoded = encodeURIComponent(fileName);
    const pathName =
      "/" +
      uploadFolder +
      "/" +
      projectFolder +
      "/" +
      directoryFolder +
      "/" +
      fileNameEncoded;
    const dbFile = await db.drawings.where("path").equals(pathName).first();
    if (dbFile) {
      await db.drawings.delete(dbFile.id);
    }
    const drawings = await db.drawings.toArray();
    tables.push({
      name: "drawings",
      rowCount: drawings.length,
      //   data: drawings,
      data: drawings.map((drawing) => ({
        area: drawing.area,
        counts: drawing.counts,
        factor: drawing.factor,
        id: drawing.id,
        lines: drawing.lines,
        path: drawing.path,
        perimeter: drawing.perimeter,
        scale: drawing.scale,
        storedEllipses: drawing.storedEllipses,
        storedHyperLinks: drawing.storedHyperLinks,
        storedLines: drawing.storedLines,
        storedRectangles: drawing.storedRectangles,
        storedTexts: drawing.storedTexts,
      })),
    });
    const exportData = {
      data: {
        databaseName,
        databaseVersion,
        tables,
      },
    };
    // saveDataToBackend(exportData);
    let data = await response.json();
    if (data.status === 200) signal.emit("refresh");
    if (setActive) {
      setActive((prevValue) => {
        return prevValue.filter((item) => {
          return item !== details.plan;
        });
      });
    }
  };
  const deleteFolder = async () => {
    let formData = new FormData();
    formData.append("path", details.plan);
    let response = await fetch(routes.deleteFolder, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${routes.__token}`,
      },
      body: formData,
    });
    const databaseName = db.name;
    const databaseVersion = db.verno;
    const tables = [];

    let data = await response.json();
    let folderPath = data.folder_path;
    const splitName = folderPath.split("/");
    const uploadFolder = splitName[0];
    const projectFolder = splitName[1];
    const directoryFolder = splitName[2];
    const folderName = splitName[3];
    const folderNameEncoded = encodeURIComponent(folderName);
    const pathName =
      "/" +
      uploadFolder +
      "/" +
      projectFolder +
      "/" +
      directoryFolder +
      "/" +
      folderNameEncoded;
    if (pathName) {
      const dbFolder = await db.drawings
        .where("path")
        .startsWith(pathName)
        .toArray();
      if (dbFolder.length > 0) {
        await Promise.all(
          dbFolder.map(async (record) => {
            await db.drawings.delete(record.id);
          })
        );
      }
    }
    const drawings = await db.drawings.toArray();
    tables.push({
      name: "drawings",
      rowCount: drawings.length,
      //   data: drawings,
      data: drawings.map((drawing) => ({
        area: drawing.area,
        counts: drawing.counts,
        factor: drawing.factor,
        id: drawing.id,
        lines: drawing.lines,
        path: drawing.path,
        perimeter: drawing.perimeter,
        scale: drawing.scale,
        storedEllipses: drawing.storedEllipses,
        storedHyperLinks: drawing.storedHyperLinks,
        storedLines: drawing.storedLines,
        storedRectangles: drawing.storedRectangles,
        storedTexts: drawing.storedTexts,
      })),
    });
    const exportData = {
      data: {
        databaseName,
        databaseVersion,
        tables,
      },
    };
    // saveDataToBackend(exportData);
    if (data.status === 200) signal.emit("refresh");
    if (setActive) {
      setActive((prevValue) => {
        return prevValue.filter((item) => {
          return item !== details.plan;
        });
      });
    }
  };
  const saveDataToBackend = async (data) => {
    try {
      const response = await fetch(`/api/sync_local_db`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${routes.__token}`,
        },
        body: JSON.stringify(data),
      });
      if (!response.ok) {
        throw new Error("Error generating proposal");
      }
      console.log("Database Synchronized");
      // const responseData = await response.json();
      // console.log('check result',responseData);
    } catch (error) {
      console.error("Error saving data to the Laravel backend:", error);
    }
  };
  return (
    <ul
      className="context-menu"
      style={{ left: `${details.x}px`, top: `${details.y}px` }}
    >
      <li onClick={openFile}>Open File</li>
      <li onClick={deleteFile}>Delete File</li>
      <li onClick={deleteFolder}>Delete Folder</li>
      <li onClick={downloadFile}>Download File</li>
    </ul>
  );
}
export default FileContext;
