import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes } from "../Constants";
import { toast } from "react-toastify";
import { projectId } from "../Constants";

function PerimeterPopup({ setDisplay, perimeterData, PerimeterValue }) {
  const [PerimeterValue1, setPerimeterValue] = useState(PerimeterValue);
  const [formDataPerimeter, setFormDataPerimeter] = useState({
    name: "",  // Takeoff Name
    trade: "", // Select Trade
    perimeterFields: [{ perimeter:PerimeterValue1??"", material: "", materialQty: "", totalLf: "", totalUnits: "" }]
  });

  const [wallMaterials, setWallMaterials] = useState([]);
  const [trade, setTrade] = useState([]);



  // Set initial data when popup is opened
  useEffect(() => {
    console.log('perimeterData');
    console.log(perimeterData);
    if (perimeterData) {
      //const { groupName, data } = perimeterData;
      console.log('perimeterData');
      console.log(perimeterData);
      if (perimeterData.name != null) {
        document.getElementById('name').readOnly = true;
        setFormDataPerimeter({ ...formDataPerimeter, name: perimeterData.name });
      }
      if (perimeterData.perimeterFields != null) {
        setFormDataPerimeter(perimeterData);
      }
      if (perimeterData.PerimeterValue != null) {
        setPerimeterValue(perimeterData.PerimeterValue);
        const elementos = document.getElementsByClassName('perimetro');
        for (let i = 0; i < elementos.length; i++) {
          elementos[i].setAttribute('readonly', true);
          elementos[i].setAttribute('value', PerimeterValue1);
          elementos[i].value=PerimeterValue1;
        }
      }



    }
  }, [perimeterData]);

  const handleInputBlur = (event) => {
    const { name, value } = event.target;
     // Llama a la funcin CalculateData si es uno de esos campos
     CalculateData({
      ...formDataPerimeter,
      [name]: value,
    });
  }
  const handleInputChange = (event) => {
    const { name, value } = event.target;

    console.log("name", name);
    console.log("value", value);

    // Verifica si el campo pertenece a los campos de nivel superior como "Takeoff Name" y "Trade"
    if (name === "name") {
      // Actualiza los campos de nivel superior sin necesidad de procesar ndices
      setFormDataPerimeter({
        ...formDataPerimeter,
        [name]: value,
      });
    }
    if (name === "trade") {
      CalculateData({
        ...formDataPerimeter,
        [name]: value,
      });
      return;
    }
    
    // Si el nombre es del tipo perimeterFields[0][perimeter], procesarlo como tal
    const match = name ? name.match(/\[(\d+)\]\[(\w+)\]/) : null;
    const index = match ? parseInt(match[1], 10) : null;
    const fieldName = match ? match[2] : null;

    // Verifica si no se pudo extraer un ndice o un campo de propiedad
    if (index === null || fieldName === null) {
      console.warn(`Formato de nombre incorrecto o falta: ${name}`);
      return; // Si no es un campo vlido, no contines
    }
    console.log("index " + index);
    console.log("fieldName " + fieldName);
    // Actualiza el array de los perimeterFields con el campo correspondiente
    const updatedFields = [...formDataPerimeter.perimeterFields];
    if (updatedFields[index]) {
      updatedFields[index][fieldName] = value;
    }

    // Actualiza formDataPerimeter con los cambios
    const updatedFormData = {
      ...formDataPerimeter,
      perimeterFields: updatedFields,
    };

    if (fieldName === "material" ) {
      CalculateData(updatedFormData);
    }

     
  };




  // Add a new row for perimeter fields
  const handleAddClick = () => {
    setFormDataPerimeter({
      ...formDataPerimeter,
      perimeterFields: [
        ...formDataPerimeter.perimeterFields,
        { perimeter: PerimeterValue1??"", material: "", materialQty: "", totalLf: "", totalUnits: "" }
      ]
    });
  };

  // Delete a row from the perimeter fields
  const handleDeleteClick = (index) => {
    const updatedPerimeterFields = [...formDataPerimeter.perimeterFields];
    updatedPerimeterFields.splice(index, 1);
    setFormDataPerimeter({ ...formDataPerimeter, perimeterFields: updatedPerimeterFields });
  };

  // Calculate and send the form data to the server
  const CalculateData = async (updatedFormData) => {
    const dataToSend = {
      template_name: formDataPerimeter.name,
      trade_name: formDataPerimeter.trade,
      formData: updatedFormData,
      type: "perimeter",
      projectId: projectId,
    };

    const response = await fetch(`/api/CalculateData`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${routes.__token}`,
      },
      body: JSON.stringify(dataToSend),
    }).catch(function (error) {
      console.log("Request failed", error);
    });

    const data = await response.json();

    if (data.status === 200) {
      document.getElementById("totales_div").innerHTML = data.resultado.totales_html;
      setFormDataPerimeter(data.resultado);
    }
    if (data.status === 400) {
      toast.error("Calculation error!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }
    if (!response.ok) {
      throw new Error("Error processing");
    }
  };

  // Handle form submission
  const handleFormSubmit = () => {
    // Save form data to local storage
    localStorage.setItem("savedFormDataPerimeter", JSON.stringify(formDataPerimeter));
    const savedData = localStorage.getItem("savedFormDataPerimeter");

    if (savedData === JSON.stringify(formDataPerimeter)) {
      toast.success("Data Saved Successfully!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    } else {
      toast.warning("Something went wrong. Please, try again!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }

    CalculateData(formDataPerimeter);

    setDisplay(false);
  };

  // Fetch wall materials and trade data
  useEffect(() => {
    const fetchWallMaterials = async () => {
      try {
        const response = await fetch(`/api/materials/${projectId}`, {
          method: "GET",
          headers: {
            Authorization: `Bearer ${routes.__token}`,
          },
        });

        if (!response.ok) {
          return;
        }

        const data = await response.json();
        setWallMaterials(data);
      } catch (error) {
        console.error("Error fetching wall materials data:", error);
      }
    };

    const fetchTradeData = async () => {
      try {
        const response = await fetch("/api/materials/divisions", {
          method: "GET",
          headers: {
            Authorization: `Bearer ${routes.__token}`,
          },
        });

        if (!response.ok) {
          return;
        }

        const data = await response.json();
        setTrade(data);
      } catch (error) {
        console.error("Error fetching trade data:", error);
      }
    };

    fetchWallMaterials();
    fetchTradeData();
  }, []);

  return createPortal(
    <Draggable>
      <div className="popup__count">
        <div className="modal-header">
          Perimeter Takeoff
          <span className="close-btn" onClick={() => setDisplay(false)}>&times;</span>
        </div>
        <form className="modal-body" onSubmit={handleFormSubmit}>
          <label className="label">Takeoff Name</label>
          <input
            type="text"
            placeholder="Takeoff Name"
            name="name"
            id="name"
            value={formDataPerimeter.name}
            onChange={handleInputChange}
            onBlur={handleInputBlur}
          />

          <label className="label">Select Trade</label>
          <select
            name="trade"
            value={formDataPerimeter.trade}
            onChange={handleInputChange}
          >
            <option value="" hidden="">
              -- Select Trade --
            </option>
            {trade.map((tradeItem) => (
              <option key={tradeItem.id} value={JSON.stringify(tradeItem)}>
                {tradeItem.name}
              </option>
            ))}
          </select>

          <label className="label text-center fw-bold">Perimeter Lineal ft</label>
          {formDataPerimeter.perimeterFields.map((field, index) => (
            <div key={index} className="form-row my-2">
              <input
                type="number"
                step="0.0001"
                name={`perimeterFields[${index}][perimeter]`}
                value={PerimeterValue1??field.perimeter}
                placeholder="Perimeter Lineal ft"
                onChange={handleInputChange}
                onBlur={handleInputBlur}
                className="perimetro"
                readOnly={(PerimeterValue1!=null)? true : false }
              />
              <select
                name={`perimeterFields[${index}][material]`}
                value={field.material}
                onChange={handleInputChange}
              >
                <option value="" hidden>-- Select Material --</option>
                {wallMaterials.map((material) => (
                  <option key={material.id} value={JSON.stringify(material)}>{material.name}</option>
                ))}
              </select>
              <input
                type="number"
                step="0.0001"
                name={`perimeterFields[${index}][materialQty]`}
                value={field.materialQty}
                placeholder="Materials Quantity"
                onChange={handleInputChange}
                onBlur={handleInputBlur}
              />
              {/* <input
                type="number"
                step="0.0001"
                name={`perimeterFields[${index}][totalLf]`}
                value={field.totalLf}
                placeholder="Total Lf"
                readOnly
                onChange={handleInputChange}
              /> */}
              <input
                type="number"
                step="0.0001"
                name={`perimeterFields[${index}][totalUnits]`}
                value={field.totalUnits}
                placeholder="Total Units"
                readOnly
                onChange={handleInputChange}
              />
              {formDataPerimeter.perimeterFields.length > 1 && (
                <button
                  className="btn btn-danger mr-2"
                  type="button"
                  onClick={() => handleDeleteClick(index)}
                >
                  <i className="fa-solid fa-trash" />
                </button>
              )}
            </div>
          ))}

          <button className="btn btn-primary mr-2" type="button" onClick={handleAddClick}>
            Add More Fields
          </button>
          <div name="totales_div" id="totales_div">tabla totales</div>
          <label className="label">Notes</label>
          <textarea
            name="note"
            rows={10}
            value={formDataPerimeter.note}
            onChange={handleInputChange}
          />

          <button className="btn btn-secondary" type="submit">
            Save
          </button>
        </form>
      </div>
    </Draggable>,
    document.querySelector("#root")
  );
}

export default PerimeterPopup;
