import { useEffect, useState } from "react";
import { projectId, routes } from "../Constants";

function Details() {
    const [data, setData] = useState({})
    useEffect(() => {
        (async () => {
            let url = routes.project + projectId;

            let response = await fetch(url, {
                method: 'GET',
                headers: {
                    "Authorization": `Bearer ${routes.__token}`
                }
            })

            let data = await response.json();

            setData(data)
        })();
    }, [])
    return (
         <div className="project-heading float-left">
            <span class="text-14 color-text fw-bold d-inline-block">
                {data?.name}
            </span>
            </div>
        // <li className="text-nowrap w-fit mr-2 hover-none">
        //     {data?.name}
        // </li>
    )
}
export default Details;