import { useEffect, useState } from 'react';
import AreaRowContext from './AreaRowContext';
import { useLocation } from "react-router-dom"
import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";
import { projectId } from "../Constants";
import { routes } from "../Constants";
import AreaPopup from "./AreaPopup";

function AreaTable( { data, setDetails }) {
    const [context, setContext] = useState(null);
    const { pathname } = useLocation();
   const [openAreaPopup, setOpenAreaPopup] = useState(false);

const [formData, setFormData] = useState();
 const current_selected_length = (state, length,name,lineTemplate) => {
         localStorage.setItem("current_selected_length",length); 
         localStorage.setItem("takeoff_name",name); 
         localStorage.setItem("lineTemplate",lineTemplate); 
        // console.log(lineTemplate,lineTemplate)
       
       //localStorage.setItem("lineTemplate",null); 
       //localStorage.setItem("current_selected_length",null); 
      // localStorage.setItem("takeoff_name",null); 
        
        ObtenData(projectId,name)
        
    }


const ObtenData = async (projectId,name) => {
        const dataToSend = {
         // template_name: name,
          trade_name: name,
         // formData: updatedFormData,
          projectId: projectId,
        };
        const response = await fetch(`/api/ObtenData`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${routes.__token}`,
          },
          body: JSON.stringify(dataToSend),
        }).catch(function (error) {                        // catch
          console.log('Request failed', error);
        });;
        const data = await response.json();
        if (data.status === 200) {
    

          setFormData(data.resultado);
          console.log(data.resultado);
          setOpenAreaPopup(true);


        }
        if (data.status === 400) {
          // HTTP status code 400
          //toast.error("calculation error !", { ...toastOptions });
          console.log(data.resultado);
          //setFormData(updatedFormData);
        }
        if (!response.ok) {
          throw new Error("Error proccesning");
        }
        //console.log("proccesing result", data);
      };




















    const handleContext = (e, index) => {
        e.preventDefault();
        setDetails(null)
        let { clientX, clientY } = e;
        setContext({x: clientX, y: clientY, index})
    }
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
    useEffect(() => {
        let contextEvent = () => {
            setContext(null)
        }
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent)
        }
    }, [])
     // Grouping areas by name
     const allAreas = data?.area || [];
     const groupedAreaData = {};
 
     allAreas.forEach((item) => {
        const name = item.meta.name;
        const value1 = item.data[0][1];
        const value2 = item.data[0][2];

        if (groupedAreaData[name]) {
            groupedAreaData[name].push({
                name: item.meta.name,
                data: item.data,
                value1: value1,
                value2: value2,
            });
           
        } else {
            groupedAreaData[name] = [
                {
                    name: item.meta.name,
                    data: item.data,
                    value1: value1,
                    value2: value2,
                },
            ];
           
        }
    });
        // Calculate the sum of value1 for each group
        Object.keys(groupedAreaData).forEach((groupName) => {
            const sumValue1 = groupedAreaData[groupName].reduce(
                (sum, entry) => sum + entry.value1,
                0
            );
            groupedAreaData[groupName].sumValue1 = sumValue1.toFixed(2);

           const mergedDataArray = groupedAreaData[groupName].reduce((mergedData, entry) => {
                return mergedData.concat(entry.data);
            }, []);

            groupedAreaData[groupName].data = mergedDataArray;
        });

        let previousRowIndex = null;
        const handleMouseInOut = async(e, index, isEntering) => {
            
            let newAreas = [...Data?.area];
            if (newAreas[index]) {
            //     // Check if the record exists in the database
                const existingRecord = await db.drawings
                    .where({ path: pathname })
                    .first();
                    // console.log('check existing record', existingRecord);
                    if (existingRecord) {
                        if (isEntering) {
                            existingRecord.area[index].meta.stroke = '#f5ed05';
                            existingRecord.area[index].meta.fill = 'rgba(238, 236, 85, 0.5)';
                            if(previousRowIndex){
                                existingRecord.area[previousRowIndex-1].meta.stroke = '#23b300';
                                existingRecord.area[previousRowIndex-1].meta.fill = '#23b3004a';
                            }
                        } 
                        if (!isEntering) {
                            existingRecord.area[index].meta.stroke = '#23b300';
                            existingRecord.area[index].meta.fill = '#23b3004a';
                            previousRowIndex = index+1;
                        }
                        // Update the record in the database
                        await db.drawings
                            .where({ path: pathname })
                            .modify({ area: existingRecord.area });
                    }
            }
         };
        // console.log('grouped area data', groupedAreaData);
    return (
       <>
         <table>
            <tbody>
                {groupedAreaData &&
                    Object.keys(groupedAreaData).map((groupName, index) => {
                        return (
                            <tr
                                key={index}
                                onContextMenu={(e) => handleContext(e, index)}
                                onClick={() =>
                                    setDetails({
                                        name: groupName,
                                        data: groupedAreaData[groupName].data,
                                    })
                                }
                            >
                                <div
                                        onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                        onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                                    >
                                <td className='w-15'>
                                    <span className="area-preview"></span>
                                </td>
                                <td>{groupName}</td>
                                <td>{groupedAreaData[groupName].sumValue1}</td>
                                <td>{groupedAreaData[groupName][0].value2}</td>
                                <td><button 
                            class="buttton custom-label"
                            onClick={() => current_selected_length(true,groupedAreaData[groupName].sumValue1,groupName,groupedAreaData[groupName][0].value2)}>
                              Details
                            </button></td> 
                                </div>
                            </tr>
                        );
                    })}
            </tbody>
        </table>
        {context?<AreaRowContext details={context}/>:<></>}
        {openAreaPopup && (
                <AreaPopup
                setDisplay={(isOpen) => setOpenAreaPopup(isOpen)}
                areaData={formData}
                //length={(length) => setOpenLengthPopup(isOpen,length)}
                />
            )}
       </>
    )
}

export default AreaTable;