  //Calculation wall material units
  const calculateWallUnit = (height, length) => {
    const wallUnit = ((height * length) / 144).toFixed(3);
    return wallUnit;
  };
  const calculateWallSqUnit = (wallUnit) => {
    const wallSqUnit = (1 / wallUnit).toFixed(3);
    return wallSqUnit;
  };
  const calculateWallCubicUnit = (length, height, width) => {
    const wallCubicArea = length * height * width;
    const wallCubicUnitInches = 1 / wallCubicArea;
    const wallCubicUnitFoot = wallCubicUnitInches / 1728;
    return wallCubicUnitFoot;
  };
  //calculation for coping material unit
  const calculateCopingUnit = (height, length) => {
    const copingUnit = ((height * length) / 144).toFixed(3);
    return copingUnit;
  };
