import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes } from "../Constants";
import { toast } from "react-toastify";
import { projectId } from "../Constants";

function OpeningPopup({ setDisplay, openingData, OpeningValue }) {
  const [OpeningValue1, setOpeningValue] = useState(OpeningValue);
  const [formDataOpening, setFormDataOpening] = useState({
    name: "", // Text
    interior_exterior: "", // Select
    shape: "", // Select
    length: "", // Numeric
    height: "", // Numeric
    bottom_elevation: "", // Numeric
    top_elevation: "", // Numeric
    sq_area: "", // Numeric
    total_units: "", // Numeric
    header: "", // Select (Material)
    header_bearing_each_side: "", // Numeric
    total: "", // Numeric
    total_sq_ft: "", // Numeric
    total_units_opening: "", // Numeric
    fill: "", // Select (Material)
    total_area: "", // Numeric
    total_cy: "", // Numeric
    header_reinforcing: "", // Select (Material)
    total_reinforcing: "", // Numeric
    quantity: "", // Numeric
    total_materials: "", // Numeric
    total_units: "", // Numeric
    jamb: "", // Select (Material)
    select_type: "", // Numeric
    total_length: "", // Numeric
    jamb_total_area: "", // Numeric
    jamb_total_units: "", // Numeric
    jam_reinforcing: "", // Select (Material)
    spaces_per_side: "", // Numeric
    quantity_per_space: "", // Numeric
    fill_jamb: "", // Select (Material)
    total_cubic_area: "", // Numeric
    total_cy_jamb: "", // Numeric
    wall_reinforcing: "", // Select (Material)
    length_wall: "", // Numeric
    reinforcing_spacing: "", // Numeric
    quantity_wall: "", // Numeric
    total_spaces: "", // Numeric
    total_lf: "", // Numeric
    total_material_units: "", // Numeric
    wall_fill: "", // Select (Material)
    sq_area_wall: "", // Numeric
    total_grout_fill_cy: "", // Numeric
    other_fill: "", // Select (Material)
    total_sq_area: "", // Numeric
    total_cy_other_fill: "", // Numeric
  });

  const [wallMaterials, setWallMaterials] = useState([]);

  // Set initial data when popup is opened
  useEffect(() => {
    if (openingData) {
      setFormDataOpening(openingData);
      if (openingData.OpeningValue != null) {
        setOpeningValue(openingData.OpeningValue);
      }
    }
  }, [openingData]);

  const handleInputBlur = (event) => {
    const { name, value } = event.target;
    CalculateData({
      ...formDataOpening,
      [name]: value,
    });
  };

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormDataOpening({
      ...formDataOpening,
      [name]: value,
    });

    // Call CalculateData on change of specific fields
    if (name === "name" || name === "interior_exterior" || name === "shape" || name === "header" || name === "fill") {
      CalculateData({
        ...formDataOpening,
        [name]: value,
      });
    }
  };

  const handleAddClick = () => {
    setFormDataOpening({
      ...formDataOpening,
      openingFields: [
        ...formDataOpening.openingFields,
        { length: "", height: "", sq_area: "", total_units: "" }
      ]
    });
  };

  const handleDeleteClick = (index) => {
    const updatedOpeningFields = [...formDataOpening.openingFields];
    updatedOpeningFields.splice(index, 1);
    setFormDataOpening({ ...formDataOpening, openingFields: updatedOpeningFields });
  };

  const CalculateData = async (updatedFormData) => {
    const dataToSend = {
      template_name: formDataOpening.name,
      trade_name: formDataOpening.name,
      formData: updatedFormData,
      type: "opening",
      projectId: projectId,
    };

    const response = await fetch(`/api/CalculateData`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${routes.__token}`,
      },
      body: JSON.stringify(dataToSend),
    }).catch(function (error) {
      console.log("Request failed", error);
    });

    const data = await response.json();

    if (data.status === 200) {
      document.getElementById("totales_div").innerHTML = data.resultado.totales_html;
      setFormDataOpening(data.resultado);
    }
    if (data.status === 400) {
      toast.error("Calculation error!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }
    if (!response.ok) {
      throw new Error("Error processing");
    }
  };

  const handleFormSubmit = () => {
    // Save form data to local storage
    localStorage.setItem("savedFormDataOpening", JSON.stringify(formDataOpening));
    const savedData = localStorage.getItem("savedFormDataOpening");

    if (savedData === JSON.stringify(formDataOpening)) {
      toast.success("Data Saved Successfully!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    } else {
      toast.warning("Something went wrong. Please, try again!", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }

    CalculateData(formDataOpening);
    setDisplay(false);
  };

  // Fetch wall materials data
  useEffect(() => {
    const fetchWallMaterials = async () => {
      try {
        const response = await fetch(`/api/materials/${projectId}`, {
          method: "GET",
          headers: {
            Authorization: `Bearer ${routes.__token}`,
          },
        });

        if (!response.ok) {
          return;
        }

        const data = await response.json();
        setWallMaterials(data);
      } catch (error) {
        console.error("Error fetching wall materials data:", error);
      }
    };

    fetchWallMaterials();
  }, []);

  return createPortal(
    <Draggable>

<div className="popup__count">
      <div className="modal-header">
        Opening Takeoff
        <span className="close-btn" onClick={() => setDisplay(false)}>
          &times;
        </span>
      </div>
      <form className="modal-body" onSubmit={handleFormSubmit}>
        <label className="label">Takeoff Name</label>
        <input
          type="text"
          name="name"
          value={formDataOpening.name}
          onChange={handleInputChange}
          placeholder="Takeoff Name"
        />

        <label className="label">Interior/Exterior</label>
        <select
          name="interior_exterior"
          value={formDataOpening.interior_exterior}
          onChange={handleInputChange}
        >
          <option value="" hidden>
            Select
          </option>
          <option value="Interior">Interior</option>
          <option value="Exterior">Exterior</option>
        </select>

        <label className="label">Shape</label>
        <select name="shape" value={formDataOpening.shape} onChange={handleInputChange}>
          <option value="" hidden>
            Select
          </option>
          <option value="Rectangular">Rectangular</option>
          <option value="Round">Round</option>
          <option value="Other">Other</option>
        </select>

        <label className="label">Length</label>
        <input
          type="number"
          name="length"
          value={formDataOpening.length}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Length"
        />

        <label className="label">Height</label>
        <input
          type="number"
          name="height"
          value={formDataOpening.height}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Height"
        />

        <label className="label">Bottom Elevation</label>
        <input
          type="number"
          name="bottom_elevation"
          value={formDataOpening.bottom_elevation}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Bottom Elevation"
        />

        <label className="label">Top Elevation</label>
        <input
          type="number"
          name="top_elevation"
          value={formDataOpening.top_elevation}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Top Elevation"
        />

        <label className="label">Square Area</label>
        <input
          type="number"
          name="sq_area"
          value={formDataOpening.sq_area}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Square Area"
        />

        <label className="label">Total Units</label>
        <input
          type="number"
          name="total_units"
          value={formDataOpening.total_units}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Units"
        />

        <label className="label">Header (Material)</label>
        <select
          name="header"
          value={formDataOpening.header}
          onChange={handleInputChange}
        >
          <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Header Bearing Each Side</label>
        <input
          type="number"
          name="header_bearing_each_side"
          value={formDataOpening.header_bearing_each_side}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Header Bearing Each Side"
        />

        <label className="label">Total</label>
        <input
          type="number"
          name="total"
          value={formDataOpening.total}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total"
        />

        <label className="label">Total Square Feet</label>
        <input
          type="number"
          name="total_sq_ft"
          value={formDataOpening.total_sq_ft}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Square Feet"
        />

        <label className="label">Total Units Opening</label>
        <input
          type="number"
          name="total_units_opening"
          value={formDataOpening.total_units_opening}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Units Opening"
        />

        <label className="label">Fill (Material)</label>
        <select name="fill" value={formDataOpening.fill} onChange={handleInputChange}>
        <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Total Area</label>
        <input
          type="number"
          name="total_area"
          value={formDataOpening.total_area}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Area"
        />

        <label className="label">Total CY</label>
        <input
          type="number"
          name="total_cy"
          value={formDataOpening.total_cy}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total CY"
        />

        {/* New Fields */}
        <label className="label">Header Reinforcing (Material)</label>
        <select
          name="header_reinforcing"
          value={formDataOpening.header_reinforcing}
          onChange={handleInputChange}
        >
           <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Total Reinforcing</label>
        <input
          type="number"
          name="total_reinforcing"
          value={formDataOpening.total_reinforcing}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Reinforcing"
        />

        <label className="label">Quantity</label>
        <input
          type="number"
          name="quantity"
          value={formDataOpening.quantity}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Quantity"
        />

        <label className="label">Total Materials</label>
        <input
          type="number"
          name="total_materials"
          value={formDataOpening.total_materials}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Materials"
        />

        <label className="label">Total Units</label>
        <input
          type="number"
          name="total_units"
          value={formDataOpening.total_units}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Units"
        />

        <label className="label">Jamb (Material)</label>
        <select name="jamb" value={formDataOpening.jamb} onChange={handleInputChange}>
        <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Select Type</label>
        <input
          type="number"
          name="select_type"
          value={formDataOpening.select_type}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Select Type"
        />

        <label className="label">Total Length</label>
        <input
          type="number"
          name="total_length"
          value={formDataOpening.total_length}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Length"
        />

        <label className="label">Jamb Total Area</label>
        <input
          type="number"
          name="jamb_total_area"
          value={formDataOpening.jamb_total_area}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Jamb Total Area"
        />

        <label className="label">Jamb Total Units</label>
        <input
          type="number"
          name="jamb_total_units"
          value={formDataOpening.jamb_total_units}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Jamb Total Units"
        />

        <label className="label">Jam Reinforcing (Material)</label>
        <select
          name="jam_reinforcing"
          value={formDataOpening.jam_reinforcing}
          onChange={handleInputChange}
        >
          <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Spaces per Side</label>
        <input
          type="number"
          name="spaces_per_side"
          value={formDataOpening.spaces_per_side}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Spaces per Side"
        />

        <label className="label">Quantity per Space</label>
        <input
          type="number"
          name="quantity_per_space"
          value={formDataOpening.quantity_per_space}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Quantity per Space"
        />

        <label className="label">Fill Jamb (Material)</label>
        <select
          name="fill_jamb"
          value={formDataOpening.fill_jamb}
          onChange={handleInputChange}
        >
           <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Total Cubic Area</label>
        <input
          type="number"
          name="total_cubic_area"
          value={formDataOpening.total_cubic_area}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Cubic Area"
        />

        <label className="label">Total CY Jamb</label>
        <input
          type="number"
          name="total_cy_jamb"
          value={formDataOpening.total_cy_jamb}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total CY Jamb"
        />

        <label className="label">Wall Reinforcing (Material)</label>
        <select
          name="wall_reinforcing"
          value={formDataOpening.wall_reinforcing}
          onChange={handleInputChange}
        >
           <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Length Wall</label>
        <input
          type="number"
          name="length_wall"
          value={formDataOpening.length_wall}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Length Wall"
        />

        <label className="label">Reinforcing Spacing</label>
        <input
          type="number"
          name="reinforcing_spacing"
          value={formDataOpening.reinforcing_spacing}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Reinforcing Spacing"
        />

        <label className="label">Quantity Wall</label>
        <input
          type="number"
          name="quantity_wall"
          value={formDataOpening.quantity_wall}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Quantity Wall"
        />

        <label className="label">Total Spaces</label>
        <input
          type="number"
          name="total_spaces"
          value={formDataOpening.total_spaces}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Spaces"
        />

        <label className="label">Total LF</label>
        <input
          type="number"
          name="total_lf"
          value={formDataOpening.total_lf}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total LF"
        />

        <label className="label">Total Material Units</label>
        <input
          type="number"
          name="total_material_units"
          value={formDataOpening.total_material_units}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Material Units"
        />

        <label className="label">Wall Fill (Material)</label>
        <select
          name="wall_fill"
          value={formDataOpening.wall_fill}
          onChange={handleInputChange}
        >
          <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Square Area Wall</label>
        <input
          type="number"
          name="sq_area_wall"
          value={formDataOpening.sq_area_wall}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Square Area Wall"
        />

        <label className="label">Total Grout Fill CY</label>
        <input
          type="number"
          name="total_grout_fill_cy"
          value={formDataOpening.total_grout_fill_cy}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Grout Fill CY"
        />

        <label className="label">Other Fill (Material)</label>
        <select
          name="other_fill"
          value={formDataOpening.other_fill}
          onChange={handleInputChange}
        >
          <option value="" hidden>-- Select Material --</option>
                    {wallMaterials.map((material) => (
                      <option key={material.id} value={material.name}>
                        {material.name}
                      </option>
                    ))}
        </select>

        <label className="label">Total Square Area</label>
        <input
          type="number"
          name="total_sq_area"
          value={formDataOpening.total_sq_area}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total Square Area"
        />

        <label className="label">Total CY Other Fill</label>
        <input
          type="number"
          name="total_cy_other_fill"
          value={formDataOpening.total_cy_other_fill}
          onChange={handleInputChange}
          step="0.0001"
          placeholder="Total CY Other Fill"
        />

        <button type="submit">Save</button>
      </form>
    </div>




       
    </Draggable>,
    document.querySelector("#root")
  );
}

export default OpeningPopup;
