import DeleteAll from "./DeleteAll"

function FileMenu() {
    return (
        <li data-tooltip="Files" className="dropdown w-65px b-right" tabIndex="0">
            <div className="text-center text-white">
                <i className="fa-light fa-file-alt"></i>
                <span class="d-block text-12">
                FILE
                </span>
            </div>
            <ul className="dropdown-menu">
                <li>
                    <label htmlFor='fileUpload' className="custom-label">Upload Files</label>
                </li>
                <li>
                    <label htmlFor='folderUpload' className="custom-label">Upload Folder</label>
                </li>
                <DeleteAll />
                <li>
                    <label className="custom-label">
                        <i className="fa-light fa-search mr"></i> Quick Search
                    </label>
                </li>
            </ul>
        </li>
    )
}
export default FileMenu