function TakeoffTable({ data }) {
    return (
        <table>
            <tbody>
                <tr>
                    <td colSpan={3}>{data?.name}</td>
                </tr>
                {data?.data.map((item, index) => {
                    return (
                        <tr key={index}>
                            <td className="w-15">
                                {item[0]}
                            </td>
                            <td>{item[1]}</td>
                            <td>{item[2]}</td>
                        </tr>
                    )
                })}

            </tbody>
        </table>
    )
}

export default TakeoffTable;