// import useSignal from '../hooks/signals';
import React, { useState, useEffect } from 'react';
import { routes } from "../Constants";
import "../Css/navbar.css";
function ProjectButton() {
    const [projects, setProjects] = useState([]);
  // const [loading, setLoading] = useState(false);

  const fetchProjectData = async () => {
    try {
      // setLoading(true);

      const response = await fetch('/api/all-projects', {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${routes.__token}`,
        },
      });

      if (!response.ok) {
        console.error('Error fetching project data');
        return;
      }
      const data = await response.json();
      setProjects(data); 
    } catch (error) {
      console.error('API call failed:', error);
    } 
    // finally {
    //   setLoading(false);
    // }
  };
  const handleProjectClick = (projectId) => {
    window.location.href = `${routes.webUrl}/${projectId}/application`;
  };
  useEffect(() => {
    // You may want to fetch data when the component mounts
    fetchProjectData();
  }, []);
    return (
      <li
        data-tooltip="Projects"
        className="dropdown b-right"
        tabIndex={0}
        onClick={fetchProjectData}
      >
        <div className="text-center text-white">
          <i className="fa fa-cube cube-icon" aria-hidden="true"></i>
        </div>
        <ul className="dropdown-menu project-btn">
          {
        //   loading ? (
        //     <li>
        //         <span>
        //             Loading...
        //         </span>
        //     </li>
        //   ) : (
            projects.map((project) => (
              <li key={project.id}
                onClick={() => handleProjectClick(project.id)}
              >
                <i className="fa fa-cube" aria-hidden="true"></i>
                <span>{project.name}</span>
              </li>
            ))
        //   )
          }
        </ul>
      </li>
    )
}
export default ProjectButton