import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";
import { useLocation } from "react-router-dom";
import { toast } from 'react-toastify';
function AreaRowContext({ details }) {
    const { pathname } = useLocation()
    const data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
    const handleHide = async (e) => {
        let newarea = data?.area.map((item, i) => {
            if (i === details.index) {
                item.hidden = true
                return item;
            }
            return item
        })
        await db.drawings.update(data?.id, { area: newarea })
        toast.success('Area Hided Successfully!', {...toastOptions});
    }
    const handleShow = async () => {
        let newarea = data?.area.map((item, i) => {
            if (i === details.index) {
                item.hidden = false
                return item;
            }
            return item
        })
        await db.drawings.update(data?.id, { area: newarea })
        toast.success('Area Shown Successfully!', {...toastOptions});
    }
    const handleShowAll = async () => {
        let newarea = data?.area.map((item, i) => {
            item.hidden = false
                return item;
        })
        await db.drawings.update(data?.id, { area: newarea })
        toast.success('All Areas Shown Successfully!', {...toastOptions});
    }
    const handleHideAll = async () => {
        let newarea = data?.area.map((item, i) => {
            item.hidden = true
                return item;
        })
        await db.drawings.update(data?.id, { area: newarea })
        toast.success('All Areas Hided Successfully!', {...toastOptions});
    }
    const handleDelete = async () => {
        let newarea = data?.area.filter((item, i) => {
            return i!==details.index
        })
        await db.drawings.update(data?.id, { area: newarea })
        toast.success('Area Deleted Successfully!', {...toastOptions});
    }
    const handleDeleteAll = async () => {
        await db.drawings.update(data?.id, { area: [] })
        toast.success('All Areas Deleted Successfully!', {...toastOptions});
    }
    const toastOptions = {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      };
    return (
        <ul className="context-menu" style={{ left: `${details.x}px`, top: `${details.y}px` }}>
            <li onClick={handleShow}>

                <i className="fa-light fa-eye"></i> Show Trace
            </li>
            <li onClick={handleShowAll}>
                <i className="fa-light fa-eye"></i> Show All
            </li>
            <li onClick={handleHide}>
                <i className="fa-light fa-eye-slash"></i> Hide Trace
            </li>
            <li onClick={handleHideAll}>
                <i className="fa-light fa-eye-slash"></i> Hide All
            </li>
            <li onClick={handleDelete}>
                <i className="fa-light fa-trash"></i> Delete
            </li>
            <li onClick={handleDeleteAll}>
                <i className="fa-light fa-trash"></i> Delete All
            </li>
        </ul>
    )
}
export default AreaRowContext;