function TraceContext({ details, setTmpData }) {
    return (
        <ul className="context-menu" style={{ left: `${details.x}px`, top: `${details.y}px` }}>
            <li onClick={() => setTmpData((tmp) => {
                tmp.pop()
                return tmp
            })}>Undo Operation</li>
            <li onClick={() => setTmpData([])}>Delete Operation</li>
        </ul>
    )
}
export default TraceContext;