import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes, projectId } from "../Constants"
import "../proposalpopup.css";

function MaterialQoutePopup({ setDisplay }) {
    const [fetchedData, setFetchedData] = useState(null);
   
    useEffect(() => {
        //ended check saved data
        const materialQoute = async (id) => {
            try {
              const response = await fetch(`/api/material-qoute/all/${id}`, {
                method: 'POST',
                headers: {
                  Authorization: `Bearer ${routes.__token}`,
                },
              });
          
              if (!response.ok) {
                // Handle error if the response is not OK (status code 2xx)
                // Example: console.log('Error generating proposal');
                return;
              }
          
              const data = await response.json();
              setFetchedData(data);
              // Assuming the response contains the proposal data
              // You can update the state or perform any other logic with the data here
              console.log('Generated proposal data:', data);
            } catch (error) {
              // Handle error if there's an issue with the API call
              // Example: console.error('API call failed:', error);
            }
          };
          materialQoute(projectId);
      }, []);
      function printProposal(dataId) {
        const printContents = document.getElementById(dataId).innerHTML;
        const originalContents = document.body.innerHTML;
      
        document.body.innerHTML = printContents;
        window.print();
      
        document.body.innerHTML = originalContents;
      }
    return (
        createPortal(
            <Draggable>
                <div className="popup__count">
                    <div className="modal-header">
                    Material Qoute Request
                        <span className="close-btn" onClick={() => { setDisplay(false); }}>&times;</span>
                    </div>
                    <div className="modal-body">
                    <button className="align-end print-button" onClick={() => printProposal("printData")}>Print</button>
                    {fetchedData && ( 
                    <div id="printData">
                        <h2 className="text-center margin-bottom">REQUEST FOR MATERIAL QOUTE</h2>
                        <p><b>{fetchedData.company}</b></p>
                        <p className="margin-bottom"><b>[Address]</b></p>

                        <p><b>[Supplier Name]</b></p>
                        <p><b>[Contract Name]</b></p>
                        <p className="margin-bottom">Re: <b>[Project Name]</b></p>

                        <p className="margin-bottom">
                        Please quote the following materials for the new <b>[PROJECT NAME]</b> in <b>[CITY AND STATE] </b>This project bids on <b>[BID DATE mm-dd-yyyy]</b>
                        </p>

                        <p className="margin-bottom">List all the materials used in the project.</p>
                        {/* {fetchedData.materials.map((material) => (
                          <div key={material.id}>
                            <p><b>Material Name:</b> {material.name}</p>
                            <p><b>Material Class:</b> {material.class_name}</p>
                          </div>
                        ))}  */}
                         <table className="margin-bottom">
                          <thead>
                            <tr>
                              <th>Material Name</th>
                              <th>Material Class</th>
                            </tr>
                          </thead>
                          <tbody>
                            {fetchedData.materials.map((material) => (
                              <tr key={material.id}>
                                <td>{material.name}</td>
                                <td>{material.class_name}</td>
                              </tr>
                            ))}
                          </tbody>
                        </table>

                        <p className="margin-bottom">
                           Thank You
                        </p>
                        <div>
                            <p>Name: <b>{fetchedData.name}</b></p>
                            <p>Company: <b>{fetchedData.company}</b></p>
                            <p>Phone: <b>{fetchedData.phone}</b></p>
                            <p>Email: <b>{fetchedData.email}</b></p>
                            <p>Website: <b>[Your Website URL]</b></p>
                        </div>
                    </div>
                    )}
                    </div>
                </div>
            </Draggable>, document.querySelector('#root'))
    )
}

export default MaterialQoutePopup;