import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { projectId } from "../Constants";
import { routes } from "../Constants";
import CoursePopup from "./CoursePopup";
import EditCoursePopup from "./EditCoursePopup";
import { useLiveQuery } from "dexie-react-hooks";
import { db } from "../database/db";
import { toast } from "react-toastify";
import useSignal from "../hooks/signals";
import Dexie from "dexie";
import "../Css/lengthpop.css";

function LengthPopupDraw({ setDisplay }) {

  const [active, setActive] = useState('')
  const [lineTemplate, setLineTemplate] = useState([]);
  const signal = useSignal();


  const [formData, setFormData] = useState({


    lineTemplate: "",
    takeoff_name: "",

  });

  var dB = new Dexie('takeoff');
  dB.version(1).stores({
    measurements: 'takeoff, name',
  });
  useEffect(() => {
    if (active !== '') {
      try {
        signal.emit('takeoff', { ...active })
      } catch (e) {
        console.log(e)
      }
    }
  }, [active, signal])


  useEffect(() => {
    (async () => {
      let url = routes.templates;
      let response = await fetch(url, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${routes.__token}`,
        },
      });
      let data = await response.json();
      //   console.log("checking response data", data.template);
      setLineTemplate(data.template);
    })();
  }, []);


  const handleSaveTemplate = async () => {

     dB.open();
    const dbData =  dB.measurements.toArray();
    if (dbData) {
       dB.measurements.clear();
    }

    const lengthName = formData.takeoff_name;
    const lineTemplate = formData.lineTemplate;
    dB.open();


    dB.measurements.add({ name: lengthName, takeoff: 'length' , lineTemplate: lineTemplate});
    setActive({ type: 'length' });
    await  dB.measurements.add({ name: lengthName, takeoff: 'length_old', lineTemplate: lineTemplate });
    setDisplay(false);
  };
  const handleInputChange = (event) => {
    const { name, value } = event.target;

    localStorage.setItem(name, value);
    setFormData({
      ...formData,
      [name]: value,
    });
   // console.log(formData);
  }






  return (
    <>
      {createPortal(
        <Draggable>
          <div className="popup__count popup-length-takeoffDraw">
            <div className="modal-header">
              Line Takeoff
              <span
                className="close-btn"
                onClick={() => {
                  setDisplay(false);
                }}
              >
                &times;
              </span>
            </div>
            <form className="modal-body">
              <div className="takeoff-name-mainDraw">
                <div className="takeoff-nameDraw">
                  <label className="label">Select Template</label>
                  <select
                    className="border-blue"
                    name="lineTemplate"
                    onChange={handleInputChange}

                  >
                    <option value="" hidden="">
                      -- Select template --
                    </option>
                    {lineTemplate.map((template) => (
                      <option
                        key={template.id}
                        value={JSON.stringify(template)}
                      >
                        {template.template_name}
                      </option>
                    ))}
                  </select>
                </div>

                <div className="takeoff-nameDraw">
                  <label className="label">TakeOff Name</label>
                  <input
                    className="border-blue"
                    type="text"
                    name="takeoff_name"
                    onChange={handleInputChange}
                  />
                </div>

              </div>


              <button
                className="btn-save mt-2"
                onClick={handleSaveTemplate}
                type="button"
              >
                Start TakeOff
              </button>
            </form>
          </div>
        </Draggable>,
        document.querySelector("#root")
      )}

    </>
  );
}

export default LengthPopupDraw;
