import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import FileContext from "./FileContext";
import { db } from '../database/db';
import { routes } from "../Constants";
import "../proposalpopup.css";
import { useLocation } from "react-router-dom";
import ProjectMeasurement from './ProjectMeasurement'; 
 

const projectDetails = () => {
    window.location.href = `${routes.webUrl}/project`;
  };
function fileName(url) {
    let arr = url.split('/')
    return arr[arr.length - 1].replace('.svg', '')
}

function FileView({ plans, activeFile, activeFiles, setActiveFiles }) {
    const { pathname } = useLocation();
    const [context, setContext] = useState(null)
    const [openFile, setOpenFile] = useState(null);
    const [isProjectMeasurementOpen, setIsProjectMeasurementOpen] = useState(false);
    const [matchingRecords, setMatchingRecords] = useState([]);
    const handleContext = (e, plan) => {
        e.preventDefault();
        let { clientX, clientY } = e;
        setContext({x: clientX, y: clientY, plan})
    }
    // console.log('checking filepath', pathname);
    const matchingPlans = [];
    const activeFunction = (plans) => {
        // Use Promise.all to wait for all queries to complete
        Promise.all(
          plans.map((plan) => {
            const splitName = plan.split('/');
            let pathName;
            if(splitName.length >= 3){
              pathName = '/'+ splitName[0] + '/' + splitName[1] +'/' + encodeURIComponent(splitName[2]);
            }
            if(splitName.length >= 4){
              pathName = '/'+ splitName[0] + '/' + splitName[1] +'/'+ splitName[2] +'/' + encodeURIComponent(splitName[3]);
            }
            if(splitName.length >= 5){
              pathName = '/'+ splitName[0] + '/' + splitName[1] +'/'+ splitName[2] +'/'+ splitName[3] +'/' + encodeURIComponent(splitName[4]);
            }
            // console.log('pathName', pathName);
            // Use 'where' to query the 'drawings' table
            return db.drawings
            .where('path')
            .equals(pathName)
            .toArray()
              .then((matchingRecords) => {
                // console.log(matchingRecords);
                if (matchingRecords.length > 0) {
                  setMatchingRecords(matchingRecords);
                    if (
                        matchingRecords[0].lines !== undefined ||
                        matchingRecords[0].storedEllipses !== undefined ||
                        matchingRecords[0].storedRectangles !== undefined ||
                        matchingRecords[0].area !== undefined ||
                        matchingRecords[0].counts !== undefined ||
                        matchingRecords[0].perimeter !== undefined ||
                        matchingRecords[0].storedHyperLinks !== undefined
                    ) {
                    if (
                        (matchingRecords[0].lines && matchingRecords[0].lines.length > 0) ||
                        (matchingRecords[0].storedEllipses && matchingRecords[0].storedEllipses.length > 0) ||
                        (matchingRecords[0].storedRectangles && matchingRecords[0].storedRectangles.length > 0) ||
                        (matchingRecords[0].area && matchingRecords[0].area.length > 0) ||
                        (matchingRecords[0].counts && matchingRecords[0].counts.length > 0) ||
                        (matchingRecords[0].perimeter && matchingRecords[0].perimeter.length > 0) ||
                        (matchingRecords[0].storedHyperLinks && matchingRecords[0].storedHyperLinks.length > 0)
                        ) {
                      //   console.log('Data found');
                      matchingPlans.push(plan); // Add the plan to matchingPlans
                    } 
                  }
                }  
              })
              .catch((error) => {
                console.error(error);
              });
          })
        ).then(() => {
          setActiveFiles([...activeFiles, ...matchingPlans]);
        });
    }
    useEffect(() => {
        activeFunction(plans);
        // console.log('set active functions is called');
      }, [plans]);
      // Function for updating it after 5 seconds
      useEffect(() => {
        // Define your function to run every 5 seconds
        function runFunction() {
            activeFunction(plans);
            
            // console.log('5 seconds function');
        }
        // Set an interval to run the function every 5 seconds
        const intervalId = setInterval(runFunction, 5000); 
        // Cleanup the interval when the component unmounts
        return () => {
          clearInterval(intervalId);
        };
      }, [plans]);
      useEffect(() => {
        // Check the open file based on the pathname every 3 seconds
        const intervalId = setInterval(() => {
            let currentPlan = pathname.split('/');
            let activePlan; // Corrected the typo here
            if (currentPlan.length >= 4) {
              // activePlan = 'projects/project537328/page-1.svg';
                activePlan = currentPlan[1] + '/' + currentPlan[2] + '/' + encodeURIComponent(currentPlan[3]);
                // activePlan = activePlan.replace(/%2520/g, '%20');
            }
            if (currentPlan.length >= 5) {
                activePlan = currentPlan[1] + '/' + currentPlan[2] + '/' + currentPlan[3] + '/' + encodeURIComponent(currentPlan[4]);
            }
            if (currentPlan.length >= 6) {
                activePlan = currentPlan[1] + '/' + currentPlan[2] + '/' + currentPlan[3] + '/' + currentPlan[4]+ '/' + encodeURIComponent(currentPlan[5]);
            }
            // console.log('active plan', activePlan);
            setOpenFile(activePlan);
        }, 3000);

        return () => {
            clearInterval(intervalId);
        };
    }, [pathname]);

    //For updating filePath
    useEffect(() => {
      const intervalId = setInterval(() => {
        saveDataToBackend(pathname);
      }, 10000);

      return () => {
          clearInterval(intervalId);
      };
  }, [pathname]);
  const saveDataToBackend = async (currentLocation) => {
     const windowCurrentLocation = window.location.href;
     const url = new URL(windowCurrentLocation);
    const windowLocation = `${url.origin}${url.pathname}`;
    // console.log('windowLocation', windowLocation);
    const dataToSend = {
      fileLocation: currentLocation,
      windowLocation: windowLocation,
    };
    try {
        const response = await fetch(`/api/current_location`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json', 
              Authorization: `Bearer ${routes.__token}`,
            },
            body: JSON.stringify(dataToSend),
          });
          if (!response.ok) {
            throw new Error('Error saving current location');
          }
    } catch (error) {
      console.error('Error saving data to the Laravel backend:', error);
    }
  };
    useEffect(() => {
        let contextEvent = () => {
            setContext(null)
        }
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent)
        }
    }, [])
    const toggleProjectMeasurement = () => {
        setIsProjectMeasurementOpen(!isProjectMeasurementOpen);
      };
    return (
        <>
          {/* For local componet */}
         {/* <a className="text-black text-14 d-inline-block measurement-btn" 
      href="javascript:void(0)">
        <img className="float-left" src="images/folder-open.svg"/>
        Open Project Measurement
      </a> */}
        <button 
            // className="open-project-btn"
            className="text-black text-14 d-inline-block return-projects-btn"
            onClick={projectDetails}
            >
            Return to Project list
        </button> 
        <button 
            // className="open-project-btn"
            className="text-black text-14 d-inline-block measurement-btn"
            onClick={toggleProjectMeasurement}
            >
            Open Project Measurement
        </button>
        {/* Conditionally render the ProjectMeasurement component */}
       {isProjectMeasurementOpen && <ProjectMeasurement plans={plans} />}
            <details className="tool__box" open>
          <summary>
              <div className="tool__box-header">
                <i class="fa fa-folder" aria-hidden="true"></i>
                        Plans and Documents
                    </div>
                </summary>
                {plans.map((plan, index) => {
                    const isActive = activeFiles.includes(plan);
                    const isOpen = plan === openFile;
                    return (
                        <Link
                            onContextMenu={(e) => handleContext(e, plan)}
                            // onClick={() => handleFileClick(plan)}
                            key={index}
                            className={`file__view ${isActive ? 'active-file' : ''} ${isOpen ? 'open-file' : ''}`}
                            to={`/${plan}`}
                        >
                        <i class="fa fa-file" aria-hidden="true"></i>
                        {/* <i class="fa fa-file-image-o" aria-hidden="true"></i> */}
                            {fileName(plan)}
                        </Link>
                    );
                })}
            </details>
            {context ? <FileContext details={context} /> : <></>}
        </>
    )
}
export default FileView;