      mat_total_break: localFormData.mat_total_break,
      lab_total_break: localFormData.lab_total_break,
      work_days_total_break: localFormData.work_days_total_break,
      mat_sale_tax: localFormData.mat_sale_tax,
      wcomp_tax: localFormData.wcomp_tax,
      weather_factor: localFormData.weather_factor,
      mat_sub_total: localFormData.mat_sub_total,
      lab_sub_total: localFormData.lab_sub_total,
      equipment_sub_total: localFormData.equipment_sub_total,
      mat_ohp_total: localFormData.mat_ohp_total,
      lab_ohp_total: localFormData.lab_ohp_total,
