import { useEffect, useState } from 'react';
import { useLocation } from "react-router-dom";
import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";
import PerimeterPopup from "./PerimeterPopup"; // Importa el popup de permetro
import PerimeterRowContext from './PerimeterRowContext';
import { projectId } from "../Constants";
import { routes } from "../Constants";

function PerimeterTable({ data, setDetails }) {
    const [context, setContext] = useState(null);
    const [openPerimeterPopup, setOpenPerimeterPopup] = useState(false); // Estado para abrir el popup de permetro
    const [perimeterData, setPerimeterData] = useState(null); // Estado para almacenar los datos del permetro seleccionado
    const { pathname } = useLocation();




    const current_selected_length = (state, length, name, lineTemplate) => {
        localStorage.setItem("current_selected_length", length);
        localStorage.setItem("takeoff_name", name);
        localStorage.setItem("lineTemplate", lineTemplate);
        // console.log(lineTemplate,lineTemplate)
        console.log("length "+length);
         
        //localStorage.setItem("lineTemplate",null); 
        //localStorage.setItem("current_selected_length",null); 
        // localStorage.setItem("takeoff_name",null); 

        ObtenData(projectId, name,length)

    }


    const ObtenData = async (projectId, name,length) => {
        const dataToSend = {
            // template_name: name,
            trade_name: name,
            // formData: updatedFormData,
            projectId: projectId,
        };
        const response = await fetch(`/api/ObtenData`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${routes.__token}`,
            },
            body: JSON.stringify(dataToSend),
        }).catch(function (error) {                        // catch
            console.log('Request failed', error);
        });;
        const data = await response.json();
        if (data.status === 200) {

            if (data.resultado != null) {
                setPerimeterData(data.resultado);
                setPerimeterData({...perimeterData,name:name,PerimeterValue:length});
            }
            else
            {
                setPerimeterData({...perimeterData,name:name,PerimeterValue:length});
            }

            console.log(data.resultado);
            setOpenPerimeterPopup(true);


        }
        if (data.status === 400) {
            // HTTP status code 400
            //toast.error("calculation error !", { ...toastOptions });
            console.log(data.resultado);
            //setFormData(updatedFormData);
        }
        if (!response.ok) {
            throw new Error("Error proccesning");
        }
        //console.log("proccesing result", data);
    };








    const handleContext = (e, index) => {
        e.preventDefault();
        setDetails(null);
        let { clientX, clientY } = e;
        setContext({ x: clientX, y: clientY, index });
    };

    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({ path: pathname }).first();
        return drawingData;
    }, [pathname]);

    useEffect(() => {
        let contextEvent = () => {
            setContext(null);
        };
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent);
        };
    }, []);

    // Grouping perimeters by name
    const allPerimeters = data?.perimeter || [];
    const groupedPerimeterData = {};

    allPerimeters.forEach((item) => {
        const name = item.meta.name;
        const value1 = item.data[0][1];
        const value2 = item.data[0][2];

        if (groupedPerimeterData[name]) {
            groupedPerimeterData[name].push({
                name: item.meta.name,
                data: item.data,
                value1: value1,
                value2: value2,
            });
        } else {
            groupedPerimeterData[name] = [
                {
                    name: item.meta.name,
                    data: item.data,
                    value1: value1,
                    value2: value2,
                },
            ];
        }
    });

    // Calculate the sum of value1 for each group of perimeters
    Object.keys(groupedPerimeterData).forEach((groupName) => {
        const sumValue1 = groupedPerimeterData[groupName].reduce(
            (sum, entry) => sum + entry.value1,
            0
        );
        groupedPerimeterData[groupName].sumValue1 = sumValue1.toFixed(2);

        // Merge the data arrays from the grouped perimeters into a single data array within the group
        const mergedDataArray = groupedPerimeterData[groupName].reduce((mergedData, entry) => {
            return mergedData.concat(entry.data);
        }, []);

        groupedPerimeterData[groupName].data = mergedDataArray;
    });

    const handleMouseInOut = async (e, index, isEntering) => {
        let newPerimeters = [...Data?.perimeter];
        if (newPerimeters[index]) {
            const existingRecord = await db.drawings.where({ path: pathname }).first();
            if (existingRecord) {
                if (isEntering) {
                    existingRecord.perimeter[index].meta.stroke = '#f5ed05';
                    existingRecord.perimeter[index].meta.fill = 'rgba(238, 236, 85, 0.5)';
                }
                if (!isEntering) {
                    existingRecord.perimeter[index].meta.stroke = 'blue';
                    existingRecord.perimeter[index].meta.fill = 'transparent';
                }
                await db.drawings.where({ path: pathname }).modify({ perimeter: existingRecord.perimeter });
            }
        }
    };

    // Maneja el clic en un permetro para abrir el popup con los detalles del permetro
    const handleRowClick = (name, data) => {
        // setPerimeterData({ name, data });
        // setOpenPerimeterPopup(true); // Abre el popup de permetro con los datos seleccionados
    };

    return (
        <>
            <table>
                <tbody>
                    {groupedPerimeterData &&
                        Object.keys(groupedPerimeterData).map((groupName, index) => {
                            return (
                                <tr
                                    key={index}
                                    onContextMenu={(e) => handleContext(e, index)}
                                    onClick={() => handleRowClick(groupName, groupedPerimeterData[groupName].data)} // Llama a la funcin para abrir el popup
                                >
                                    <div
                                        onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                                        onMouseLeave={(e) => handleMouseInOut(e, index, false)}
                                    >
                                        <td className='w-15'>
                                            <span className="perimeter-preview"></span>
                                        </td>
                                        <td>{groupName}</td>
                                        <td>{groupedPerimeterData[groupName].sumValue1}</td>
                                        <td>{groupedPerimeterData[groupName][0].value2}</td>
                                        <td><button
                                            class="buttton custom-label"
                                            onClick={() => current_selected_length(true, groupedPerimeterData[groupName].sumValue1, groupName, groupedPerimeterData[groupName][0].value2)}>
                                            Details {groupedPerimeterData[groupName].sumValue1}
                                        </button></td>
                                    </div>
                                </tr>
                            );
                        })}
                </tbody>
            </table>

            {context ? <PerimeterRowContext details={context} /> : <></>}

            {/* Abre el popup de permetro si est abierto */}
            {openPerimeterPopup && (
                <PerimeterPopup
                    setDisplay={setOpenPerimeterPopup} // Funcin para cerrar el popup
                    perimeterData={perimeterData} // Pasa los datos del permetro seleccionado
                />
            )}
        </>
    );
}

export default PerimeterTable;
