import { useEffect, useState } from "react";
import useSignal from "../hooks/signals";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";
import { useLocation } from 'react-router-dom';
import TraceContext from "./TraceContext";
import objects from "../objects";

function ScaleBoard({ transform }) {
    const signal = useSignal()
    const [display, setDisplay] = useState(false)
    const { pathname } = useLocation()
    const [active, setActive] = useState(false)
    const [scale, setScale] = useState('')
    const [tmpData, setTmpdata] = useState([])
    const [context, setContext] = useState(null);
    const [docDistance, setDocDistance] = useState('')
    const [actual, setActual] = useState('')
    const [trace, setTrace] = useState(false)
    const [pop, setPop] = useState(true)
    const data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        if (!drawingData) {
            db.drawings.put({ path: pathname, scale: '' });
        }
        return drawingData;
    }, [pathname])
    const setDocScale = async (e) => {
        setDisplay(false)
        setPop(true)
        setTrace(false);
        setActual('');
        setDocDistance('')
        if((docDistance !== '') && (actual !=='')) {
            let text = `${docDistance}" = ${actual}'`
            let factor = parseFloat(actual) / parseFloat(docDistance)
            db.drawings.update(data?.id, { scale: text, factor: factor })
            return false;
        }
        let object = objects[scale];
        db.drawings.update(data?.id, { scale: object.text, factor: object.factor })

    }
    useEffect(() => {
        let contextEvent = () => {
            setContext(null)
        }
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent)
            setTmpdata([])
        }
    }, [pathname, data])
    const handleContext = (e) => {
        if (!tmpData.length) {
            return
        }
        e.preventDefault();
        let { clientX, clientY } = e;
        setContext({ x: clientX, y: clientY })
    }
    useEffect(() => {
        setActive(false)
        let timeOut = setTimeout(() => {
            setActive(true)
        }, 500)
        return () => {
            clearTimeout(timeOut)
        }
    }, [transform])
    useEffect(() => {
        let callback = () => {
            setDisplay(true)
        }
        signal.listen('scale.start', callback)

        return () => {
            signal.unsubscribe('scale.start', callback)
        }

    }, [display]) // eslint-disable-line react-hooks/exhaustive-deps
    const makePathFromOffset = (path) => {
        let pathStr = ``;
        path.forEach((p, i) => {
            if (i === 0) {
                pathStr += `M${p.x} ${p.y} `;
                return
            }
            pathStr += `L${p.x} ${p.y} `
        })
        return pathStr
    }
    const handleMouseMove = (e) => {
        if (tmpData.length) {
            let { offsetX, offsetY } = e.nativeEvent;
            let localtmp = tmpData;
            localtmp[localtmp.length - 1] = { x: offsetX, y: offsetY }
            setTmpdata([...localtmp])
        }
    }
    const magnificationFactor = () => {
        let canvas = document.querySelector('canvas')
        if (!canvas) return 27;

        let width = canvas.width;
        return (70000 / (width / 4))
    }
    const setDocumentDistance = () => {
        if(tmpData.length < 2) return;
        let totalLength = 0;
        let newArr = tmpData;
        let magFactor = magnificationFactor()
        for (let i = 0; i < newArr.length - 2; i++) {
            let x1 = newArr[i].x;
            let y1 = newArr[i].y
            let x2 = newArr[i + 1].x;
            let y2 = newArr[i + 1].y;

            let distance = ((x1 - x2) ** 2 + (y1 - y2) ** 2) ** (1 / 2)
            let unscaled = distance / (magFactor * 96);
            totalLength+=unscaled
        }
        setTmpdata([])
        setDocDistance(parseFloat(totalLength.toFixed(3)))
    }
    const lineDrawing = (e) => {
        if (!active) return;
        if(!trace) return;
        if (e.detail >= 2) {
            setPop(true)
            setTrace(false)
            return setDocumentDistance(e)
        }
        let { offsetX, offsetY } = e.nativeEvent;
        if (!tmpData.length) {
            setTmpdata([{ x: offsetX, y: offsetY }, { x: offsetX, y: offsetY }])
            return
        }
        setTmpdata((tmp) => [...tmp, { x: offsetX, y: offsetY }])
    }
    const ScalePopup = () => {
        return (
            createPortal(
                <Draggable>
                    <div className="modal" style={{display: pop?'unset':'none'}}>
                        <div className="modal-header">
                            Scale Configuration
                            <span className="close-btn" onClick={() => {setDisplay(false); setDocDistance('')}}>&times;</span>
                        </div>
                        <div className="modal-body">
                            <label className="label">Standard Scale</label>
                            <select onChange={(e) => setScale(e.target.value)} value={scale}>
                                <option value="" hidden>Scale</option>
                                <optgroup label="Architectural">
                                    <option value={0}>1/128" = 1'</option>
                                    <option value={1}>1/64" = 1'</option>
                                    <option value={2}>1/32" = 1'</option>
                                    <option value={3}>1/16" = 1'</option>
                                    <option value={4}>3/32" = 1'</option>
                                    <option value={5}>1/8" = 1'</option>
                                    <option value={6}>3/16" = 1'</option>
                                    <option value={7}>1/4" = 1'</option>
                                    <option value={8}>3/8" = 1'</option>
                                    <option value={9}>1/2" = 1'</option>
                                    <option value={10}>3/4" = 1'</option>
                                    <option value={11}>1" = 1'</option>
                                    <option value={12}>1 1/2" = 1'</option>
                                    <option value={13}>3" = 1'</option>
                                </optgroup>
                                <optgroup label="Civil">
                                    <option value={14}>1" = 10'</option>
                                    <option value={15}>1" = 20'</option>
                                    <option value={16}>1" = 30'</option>
                                    <option value={17}>1" = 40'</option>
                                    <option value={18}>1" = 50'</option>
                                    <option value={19}>1" = 60'</option>
                                    <option value={20}>1" = 70'</option>
                                    <option value={21}>1" = 80'</option>
                                    <option value={22}>1" = 90'</option>
                                    <option value={23}>1" = 100'</option>
                                    <option value={24}>1" = 200'</option>
                                    <option value={25}>1" = 300'</option>
                                    <option value={26}>1" = 500'</option>
                                    <option value={27}>1" = 1000'</option>
                                </optgroup>
                                <optgroup label="Custom">
                                    <option value={28}>1/160" = 1'</option>
                                    <option value={29}>13/55" = 1'</option>
                                </optgroup>
                            </select>
                            <label className="label">Document Distance</label>
                            <input type='number' step={0.01} placeholder='Inch' defaultValue={docDistance} readOnly />
                            <label className="label">Actual Distance</label>
                            <input type='number' step={0.01} placeholder='Feet' value={actual} onChange={(e) => {setActual(e.target.value)} }/>
                            <div className="mt-2">
                                <button className="btn btn-primary mr-2" onClick={setDocScale}>OK</button>
                                <button className="btn btn-secondary"
                                    onClick={() => {
                                        setPop(false)
                                        setTrace(true)
                                    }}
                                >Calibrate</button>
                            </div>
                        </div>
                    </div>
                </Draggable>
                , document.querySelector('#root'))

        )
    }
    if (!display) return <></>
    return (
        <>
            <svg className="svg__board-scale" onContextMenu={handleContext} onMouseMove={handleMouseMove} onClick={lineDrawing} style={{ cursor: trace?'copy':'unset' }}>
                <defs>
                    <marker id="arrow"
                        viewBox="0 0 10 10"
                        refX="7" refY="5"
                        markerWidth="7"
                        markerHeight="4"
                        orient="auto-start-reverse">
                        <path fill="#000" d="M 0 0 L 10 5 L 0 10 z" />
                    </marker>
                    <marker id="circle"
                        viewBox="0 0 10 10"
                        refX="3"
                        refY="3"
                        markerWidth="6"
                        markerHeight="6">
                        <circle cx="4" cy="4" r="4" fill="#ffd800a3" stroke="#000" strokeWidth="0.5" />
                    </marker>
                </defs>
                <g strokeWidth={3 / transform.scale} className='line_group-temp' stroke="blue">
                    <path title="Title" markerStart="url(#circle)" markerMid="url(#circle)" markerEnd="url(#circle)" fill="transparent" d={makePathFromOffset(tmpData)}></path>
                </g>
            </svg>
            <ScalePopup />
            {context ? createPortal(<TraceContext details={context} setTmpData={setTmpdata} />, document.querySelector('#root')) : <></>}
        </>
    )
}
export default ScaleBoard;