import useSignal from '../hooks/signals'
import { db } from '../database/db'
import { useLocation } from 'react-router-dom'
import { useLiveQuery } from 'dexie-react-hooks'
function ScaleButton() {
    const signal = useSignal()
    const { pathname } = useLocation()
    const data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        if (!drawingData) {
            db.drawings.put({ path: pathname, scale: '' });
        }
        return drawingData;
    }, [pathname])
    return (
        <li className="scale_view_container w-145px .mr-0 b-right">
            <span className="view-scale">
                <span className="d-inline-block scale-set-view">
                {data?.scale || 'Scale'}
                </span>
                <span tabIndex="0" className="select-scale float-right" onClick={() => signal.emit('scale.start')}>
                    <i className="fa-regular fa-weight-scale"></i>
                </span>
            </span>
        </li>
    )
}
export default ScaleButton