
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { db } from "../database/db";
import { useState } from "react";
import { useEffect } from "react";
import { toast } from "react-toastify";
import { routes } from "../Constants";
import "../Css/course.css";


const initialValues = {
    name: "",
    band_height: "",
    bottom_elevation: "",
    top_elevation: "",
    material_sq_ft: "",
    band_material: "",
    select_wall: "",
    fill_material: "",
    rebar_material: "",
    rebar_overlap: "",
    total_per_each: "",
    rebar_quantity: "",
    sq_grouted_per_cy: "",
    detail: "",
    
    total_course: "0",
    total_material_unit: "",
    total_rebar_length: "",
    total_rebar_lf: "0",
    rebar_lf_per_ton: "0",
    area_grouted_sq: "",
    total_grout_cy: "",
    total_area_grout_sq: "",
}
export default function EditCoursePopup({ editPop, setDisplay, materials, courseId }) {
    console.log(courseId)
    const [wallMaterials, setWallMaterials] = useState([]);
    const [formData, setFormData] = useState({ ...initialValues })
     useEffect(() => {
        const fetchData = async () => {
            try {
                // Fetch course data based on the courseId
                const courseData = await db.course_band.get(courseId);
                console.log('courseData', courseData);
                // Set the initial values in formData
                setFormData({
                    // Assuming your courseData has properties like name, top_elevation, band_height, etc.
                    name: courseData.name,
                    band_height: courseData.band_height,
                    bottom_elevation: courseData.bottom_elevation,
                    top_elevation: courseData.top_elevation,
                    material_sq_ft: courseData.material_sq_ft,
                    band_material: courseData.band_material,
                    select_wall: courseData.select_wall,
                    fill_material: courseData.fill_material,
                    rebar_material: courseData.rebar_material,
                    rebar_overlap: courseData.rebar_overlap,
                    total_per_each: courseData.total_per_each,
                    rebar_quantity: courseData.rebar_quantity,
                    sq_grouted_per_cy: courseData.sq_grouted_per_cy,
                    detail: courseData.detail,
                    // Add other properties as needed
                });

                // Set other state values if needed
                setWallMaterials(courseData.materials || []);
            } catch (error) {
                console.error('Error fetching course data:', error);
            }
        };

        // Fetch data when the component mounts
        if (editPop && courseId) {
            fetchData();
        }
    }, [editPop, courseId]);
    const handleUpdate = async (e) => {
        e.preventDefault()
        const courseData = await db.course_band.get(courseId);
          const updatedCourseData = {
            ...courseData,
            name: formData.name,
            band_height: formData.band_height,
            bottom_elevation: formData.bottom_elevation,
            top_elevation: formData.top_elevation,
            band_material: formData.band_material,
            select_wall: formData.select_wall,
            fill_material: formData.fill_material,
            rebar_material: formData.rebar_material,
            rebar_overlap: formData.rebar_overlap,
            total_per_each: formData.total_per_each,
            rebar_quantity: formData.rebar_quantity,
            sq_grouted_per_cy: formData.sq_grouted_per_cy,
            detail: formData.detail,
            // Add other properties as needed
        };
        // Update the course data in the database
        await db.course_band.put(updatedCourseData);
        setDisplay(false);
        toast.success('Band Updated Successfully!', { ...toastOptions });
    }
 useEffect(() => {
     const fetchWallMaterials = async () => {
            try {
              const response = await fetch('/api/materials', {
                method: 'GET',
                headers: {
                  Authorization: `Bearer ${routes.__token}`,
                },
              });
      
              if (!response.ok) {
                // Handle error if the response is not OK (status code 2xx)
                // Example: console.log('Error fetching wall materials data');
                return;
              }
      
              const data = await response.json();
              setWallMaterials(data);
            } catch (error) {
              // Handle error if there's an issue with the API call
              // Example: console.error('API call failed:', error);
            }
          };
          fetchWallMaterials();
      }, []);
    // console.log(materials)
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            top_elevation: (
                (parseFloat(formData.band_height) || 0) +
                (parseFloat(formData.bottom_elevation) || 0) ||
                0
            ).toFixed(3),
        }))
    }, [
        formData.band_height,
        formData.bottom_elevation
    ])
    console.log('check band material height', formData.band_material.length);
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_per_each: (
                (parseFloat(formData.rebar_overlap) || 0) +
                (parseFloat(formData.band_material.length / 12) || 0) ||
                0
            ).toFixed(3),
        }))
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ]);
     const toastOptions = {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
    };
    return (
        createPortal(
            <Draggable>
                <div className="modal" style={{ display: editPop ? 'unset' : 'none' }}>
                    <div className="modal-header">
                        Edit Course
                        <span className="close-btn" onClick={() => setDisplay(false)}>&times;</span>
                    </div>
                    <form className="modal-body" onSubmit={handleUpdate}>
                        <div className="course-input">
                            <label className="label">Name</label>
                            <input
                                className="border-blue"
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Band Height</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.band_height}
                                onChange={(e) => setFormData({ ...formData, band_height: e.target.value })}
                                name="band_height" />
                        </div>
                        <div className="course-input">
                            <label className="label">Bottom Elevation</label>
                            <input
                                className="border-blue"
                                type="number"
                                name="bottom_elevation"
                                value={formData.bottom_elevation}
                                onChange={(e) => setFormData({ ...formData, bottom_elevation: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Top Elevation</label>
                            <input
                                type="number"
                                name="top_elevation"
                                value={formData.top_elevation}
                                onChange={(e) => setFormData({ ...formData, top_elevation: e.target.value })}
                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Material sq ft</label>
                            <input
                                type="number"
                                name="material_sq_ft"
                                value={formData.material_sq_ft}
                                onChange={(e) => setFormData({ ...formData, material_sq_ft: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">
                                Band Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, band_material: wallMaterials[e.target.value] })}
                                name="band_material"
                            >
                                 <option value="">{formData.band_material?.name || '--select--'}</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Fill Material</label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, fill_material: wallMaterials[e.target.value] })}
                                name="fill_material">
                                <option value="">{formData.fill_material?.name || '--select--'}</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label
                                className="label"
                            >
                                Rebar Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, rebar_material: wallMaterials[e.target.value] })}
                                name="rebar_material">
                                <option value="">{formData.rebar_material?.name || '--select--'}</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Overlap</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_overlap}
                                onChange={(e) => setFormData({ ...formData, rebar_overlap: e.target.value })}
                                name="rebar_overlap" />
                        </div>
                        <div className="course-input">
                            <label className="label">Total per each</label>
                            <input
                                type="number"
                                name="total_per_each"
                                value={formData.total_per_each}
                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Quantity</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_quantity}
                                onChange={(e) => setFormData({ ...formData, rebar_quantity: e.target.value })}
                                name="rebar_quantity"
                            />
                        </div>
                        <div className="course-input">
                        <label className="label">Sq Grouted per cy </label>
                        <input
                            className="border-blue"
                            name="sq_grouted_per_cy"
                            type={'number'}
                            value={formData.sq_grouted_per_cy}
                                onChange={(e) => setFormData({ ...formData, sq_grouted_per_cy: e.target.value })}
                        />
                        </div>
                        <div className="course-input">
                            <label className="label">
                                Select Wall
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, select_wall: e.target.value })}
                                name="select_wall">
                                <option value="" hidden>{formData.select_wall || "-- select --"}</option>
                                <option value="main">Main</option>
                                 {/* <option value="top">Top</option>
                                <option value="bottom">Bottom</option>
                                <option value="top_botton">Top Bottom</option> */}
                            </select>
                        </div>
                        <div className="course-input-textarea">
                            <label className="label">Details</label>
                            <textarea 
                                name="detail" 
                                rows={5} 
                                value={formData.detail || ""}
                                onChange={(e) => setFormData({ ...formData, detail: e.target.value })}
                            />
                        </div>
                        <button type="submit" className="btn btn-primary mt-2">Update</button>
                    </form>
                </div>
            </Draggable>,
            document.querySelector('#root')
        )
    )
}