import React from 'react';
import { routes } from "../Constants";
import "../Css/navbar.css";
function ProjectDetails() {
  const assetPath = routes.assetPath;
  const projectDetails = () => {
    window.location.href = `${routes.webUrl}/project`;
  };
 
    return (
        <li
        data-tooltip="Project Details"
        className="dropdown logo-image"
        onClick={projectDetails}
      >
        <img
          src={assetPath +"images/project-page-logo.svg"}
        alt="Project Details"
      />
        {/* Project Details */}
      </li>
    )
}
export default ProjectDetails