import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes } from "../Constants";
import { toast } from "react-toastify";
import { projectId } from "../Constants";




function AreaPopup({ setDisplay, walltotalarea,areaData=null }) {


  // Set initial data when popup is opened
  useEffect(() => {
    if (areaData) {
      const { groupName, data } = areaData;
      setformDataArea({
        ...formDataArea,
        name: groupName,
        perimeterFields: data
      });
    }
  }, [areaData]);

  const [clickedRowIndex, setClickedRowIndex] = useState(null);
  const [newRowsAdjusment, setNewRowsAdjusment] = useState([]);



  const [trade, setTrade] = useState([])
  const [wall_total_area, setwall_total_area] = useState([walltotalarea])
  const [wallMaterials, setWallMaterials] = useState([]);
  // const [additionalMaterials, setAdditionalMaterials] = useState([""]);
  const [materialPerSqft, setMaterialPerSqft] = useState([""]);
  const [quantityPerSqft, setQuantityPerSqft] = useState([""]);

  const [materialDeduct, setMaterialDeduct] = useState([""]);
  // const [selectedId, setSelectedId] = useState('');
  // Add a state to store form data
  const [formDataArea, setformDataArea] = useState({
    name: "",
    trade: "",
    main_material: "",
    underlay_material: "",
    Area_thickness: "",
    rise_drop_rise: "",
    rise_drop_thickness : "",
    underlay_material : "",
    underlay_sq_ft  : "",
    underlay_thickness  : "",
    note: "",
    adjustmentDatas: [
      {
        adjustment_material: "0",

        adjustment_description: "",
         
        adjustment_type: "add",
        adjustment_qty: "0",
        adjustment_unit: "sqft",
        adjustment_measured_qty: "0",

      },
    ],
    additionalMaterials: [
      {
        total_cubic_ft: "0",
        cubicFt: "0",
        thickness: "0",
        material: "0",

        total_units: "",

      },
    ],
    additionalDatas: [],
    // Add other form fields here...
  });
  const [additionalMaterials, setAdditionalMaterials] = useState([
    { material: "0", thickness: "0", cubicFt: "0", totalUnits: "0" },
  ]);

  const toastOptions = {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: 0,
  };
  const CalculateData = async (updatedFormData) => {
    const dataToSend = {
      template_name: formDataArea.name,
      trade_name: formDataArea.trade,
      formData: updatedFormData,
      type: "area",
      projectId: projectId,
    };
    const response = await fetch(`/api/CalculateData`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${routes.__token}`,
      },
      body: JSON.stringify(dataToSend),
    }).catch(function (error) {                        // catch
      console.log('Request failed', error);
    });;
    const data = await response.json();
    if (data.status === 200) {

      // HTTP status code in the range 200-299

      // toast.success("Data proccesed Successfully!", { ...toastOptions });
      /* console.log("wall_height", data.resultado.wall_height);
      console.log(data.resultado); */
      // var t = document.createElement('template');
      // t.innerHTML = data.totales_html;
      // data.totales_html=t.content;


      document.getElementById("totales_div").innerHTML = data.resultado.totales_html;
      setformDataArea(data.resultado);
    }
    if (data.status === 400) {
      // HTTP status code 400
      toast.error("calculation error !", { ...toastOptions });
      console.log(data.resultado);
      //setformDataArea(updatedFormData);
    }
    if (!response.ok) {
      throw new Error("Error proccesning");
    }
    //console.log("proccesing result", data);
  };

  const handleAddClickAdjustment = (rowIndex) => {
    setClickedRowIndex(rowIndex);
    // Get the data for the selected row
    const selectedRowData = formDataArea.adjustmentDatas[rowIndex];
    // Find the count of rows added for the specific row
    const rowCount = (formDataArea.adjustmentDatas[rowIndex]?.count || 0) + 1; // Increment rowCount by 1
    // Create a new line object with the generated name, amount, and other default values
    const newLine = {



      adjustment_description: selectedRowData.adjustment_description,
      adjustment_material: selectedRowData.adjustment_material,
      adjustment_type: selectedRowData.adjustment_type,
      adjustment_qty: selectedRowData.adjustment_qty,
      adjustment_unit: selectedRowData.adjustment_unit,
      adjustment_measured_qty: selectedRowData.adjustment_measured_qty,

      isNew: true,
    };

    // Insert the new line at the desired position
    formDataArea.adjustmentDatas.splice(rowIndex + 1, 0, newLine);

    // finalCalculations(formDataArea);
    // Add the index of the new row to the newRows state array
    setNewRowsAdjusment((prevNewRows) => [...prevNewRows, rowIndex + 1]);
  };
  const handleDeleteAdjustment = (index) => {
    setformDataArea((prevData) => {
      const newadjustmentDatas = [
        ...prevData.adjustmentDatas.slice(0, index),
        ...prevData.adjustmentDatas.slice(index + 1),
      ];

      return {
        ...prevData,
        adjustmentDatas: newadjustmentDatas,
      };
    });
  };
  const handleChangeAdjustments = (event) => {
    const { name, value } = event.target;
    const indexMatch = name.match(/\[(\d+)\]/);
    const index = indexMatch ? parseInt(indexMatch[1], 10) : 0;
    const indexedName = name.replace(/\[\d+\]/, "");
    //var updatedFormData = { ...formDataArea };
    var updatedFormData = {
      ...formDataArea,
      [name]: value,
    };

    updatedFormData.adjustmentDatas[index][indexedName] = value;

    if (indexedName === "adjustment_material") {
      const selectedMaterial = JSON.parse(value);

      updatedFormData.adjustmentDatas[index].adjustment_unit = selectedMaterial.material_type_id;
      console.log("actualizando a unidad:selectedMaterial.material_type_id: ", selectedMaterial.material_type_id)
    }

    //setformDataArea(updatedFormData);
    setformDataArea(updatedFormData);
  };



  //aditional materials
  const handleAddClickAdditionalMaterial = () => {
    setAdditionalMaterials([
      ...additionalMaterials,
      { material: "", thickness: "", cubicFt: "", totalUnits: "" },
    ]);
  };

  const handleDeleteClickAdditionalMaterial = (index) => {
    if (additionalMaterials.length > 1) {
      const updatedMaterials = [...additionalMaterials];
      updatedMaterials.splice(index, 1); // Elimina el material en el ndice especificado
      setAdditionalMaterials(updatedMaterials);
    }
  };
  const handleInputChangeAdditionalMaterial = (event) => {
    console.log("change adicional");
    const { name, value } = event.target;
    const index = Number(name.match(/\d+/)[0]); // Extrae el ndice del nombre del campo

    

    // Expresin regular para extraer el ndice y el nombre del campo
    const match = name.match(/additional_material\[(\d+)\]\[(\w+)\]/);

    if (match) {
      const index = parseInt(match[1], 10); // Obtiene el ndice (1 en este caso)
      const fieldName = match[2]; // Obtiene el nombre del campo (material)

      console.log("Index:", index); // 1
      console.log("Field name:", fieldName); // material
      
      console.log("name " + name);      
      console.log("value " + value);
      // Actualiza el estado de additionalMaterials basado en el ndice y el nombre del campo
      const updatedAdditionalMaterials = [...additionalMaterials];
      console.log(updatedAdditionalMaterials);
      updatedAdditionalMaterials[index][fieldName] = value;
      
      // Actualiza el estado de additionalMaterials
      setAdditionalMaterials(updatedAdditionalMaterials);
      
      // Refleja los cambios en formDataArea
      setformDataArea({
        ...formDataArea,
        additionalMaterials: updatedAdditionalMaterials,
      });
      var updatedFormData = {
        ...formDataArea
         
      };
      CalculateData(updatedFormData);
    }
  };




  const handleInputChange = (event) => {
    const { name, value } = event.target;
    if (name === "main_material") {
      // Handle changes for main material
      setformDataArea({
        ...formDataArea,
        [name]: value,
      });
    } else if (name.startsWith("additional_material[")) {
      // Handle changes for additional materials
      const index = Number(name.match(/\d+/)[0]); // Extract the index from the name attribute
      const updatedMaterials = [...additionalMaterials];
      updatedMaterials[index] = value;
      setAdditionalMaterials(updatedMaterials);

      // Update the formDataArea with the new additional materials array
      setformDataArea({
        ...formDataArea,
        additional_material: updatedMaterials,
      });
    } else if (name.startsWith("material_per_sq_ft[")) {
      // Handle changes for additional materials
      const index = Number(name.match(/\d+/)[0]); // Extract the index from the name attribute
      const updatedMaterialsPerSqft = [...materialPerSqft];
      updatedMaterialsPerSqft[index] = value;
      setMaterialPerSqft(updatedMaterialsPerSqft);

      // Update the formDataArea with the new additional materials array
      setformDataArea({
        ...formDataArea,
        material_per_sq_ft: updatedMaterialsPerSqft,
      });
    } else if (name.startsWith("material_deduct[")) {
      // Handle changes for additional materials
      const index = Number(name.match(/\d+/)[0]); // Extract the index from the name attribute
      const updatedMaterialsDeduct = [...materialDeduct];
      updatedMaterialsDeduct[index] = value;
      setMaterialDeduct(updatedMaterialsDeduct);

      // Update the formDataArea with the new additional materials array
      setformDataArea({
        ...formDataArea,
        material_deduct: updatedMaterialsDeduct,
      });
    } else {
      // Handle changes for other form fields
      setformDataArea({
        ...formDataArea,
        [name]: value,
      });
      var updatedFormData = {
        ...formDataArea,
        [name]: value,
      };
      CalculateData(updatedFormData);
    }




    if (name.startsWith("material_per_sq_ft[")) {
      // Handle changes for material_per_sq_ft
      const index = Number(name.match(/\d+/)[0]);
      const updatedMaterialsPerSqft = [...materialPerSqft];
      updatedMaterialsPerSqft[index] = value;
      setMaterialPerSqft(updatedMaterialsPerSqft);
    } else if (name.startsWith("quantity_per_sq_ft[")) {
      // Handle changes for quantity_per_sq_ft
      const index = Number(name.match(/\d+/)[0]);
      const updatedQuantityPerSqft = [...quantityPerSqft];
      updatedQuantityPerSqft[index] = value;
      setQuantityPerSqft(updatedQuantityPerSqft);
    } else {
      // Handle changes for other fields
      setformDataArea({
        ...formDataArea,
        [name]: value,
      });
    }

  };


  // Add a function to handle form submission and save data to local storage
  const handleFormSubmit = () => {
    // Save formDataArea to local storage (convert it to a JSON string before saving)
    localStorage.setItem("savedformDataArea", JSON.stringify(formDataArea));
    const savedData = localStorage.getItem("savedformDataArea");
    if (savedData === JSON.stringify(formDataArea)) {
      // Data was successfully saved
      toast.success('Data Saved Successfully!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    } else {
      // There was an issue saving the data
      toast.warning('Something went wrong. Please, try again!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });
    }
    // You can do other actions here if needed after saving the data.
    // For example, close the popup or navigate to another page.
    setDisplay(false);
  };
  const handleAddClick = () => {
    setAdditionalMaterials([...additionalMaterials, ""]);
  };
  const handleDeleteClick = () => {
    if (additionalMaterials.length > 1) {
      const updatedMaterials = [...additionalMaterials];
      updatedMaterials.pop();
      setAdditionalMaterials(updatedMaterials);
    }
  };
  // const handleAddClickPerSqft = () => {
  //   setMaterialPerSqft([...materialPerSqft, ""]);
  // };
  // const handleDeleteClickPerSqft = () => {
  //   if (materialPerSqft.length > 1) {
  //     const updatedMaterialsPerSqft = [...materialPerSqft];
  //     updatedMaterialsPerSqft.pop();
  //     setMaterialPerSqft(updatedMaterialsPerSqft);
  //   }
  // };

  const handleAddClickPerSqft = () => {
    setMaterialPerSqft([...materialPerSqft, ""]);
    setQuantityPerSqft([...quantityPerSqft, ""]);
  };

  const handleDeleteClickPerSqft = () => {
    if (materialPerSqft.length > 1) {
      const updatedMaterialsPerSqft = [...materialPerSqft];
      updatedMaterialsPerSqft.pop();
      setMaterialPerSqft(updatedMaterialsPerSqft);

      const updatedQuantitiesPerSqft = [...quantityPerSqft];
      updatedQuantitiesPerSqft.pop();
      setQuantityPerSqft(updatedQuantitiesPerSqft);
      var updatedFormData = {
        ...formDataArea
         
      };
      CalculateData(updatedFormData);
    }
  };
  const handleAddClickDeduct = () => {
    setMaterialDeduct([...materialDeduct, ""]);
  };
  const handleDeleteClickDeduct = () => {
    if (materialDeduct.length > 1) {
      const updatedMaterialsDeduct = [...materialDeduct];
      updatedMaterialsDeduct.pop();
      setMaterialDeduct(updatedMaterialsDeduct);
    }
  };

  useEffect(() => {
    // Check if data is saved in local storage
    const savedData = localStorage.getItem("savedformDataArea");

    if (savedData) {
      // If data exists in local storage, you can parse the JSON string back to an object
      const formDataAreaFromLocalStorage = JSON.parse(savedData);
      console.log("Data is saved:", formDataAreaFromLocalStorage);

      // Update the formDataArea state with the saved data
      setformDataArea(formDataAreaFromLocalStorage);
      // Check if there are additional materials in the saved data
      if (formDataAreaFromLocalStorage.additional_material) {
        // If there are additional materials, set them to the additionalMaterials state
        setAdditionalMaterials(formDataAreaFromLocalStorage.additional_material);
      } else {
        // If no additional materials, initialize the additionalMaterials state as an empty array
        setAdditionalMaterials([]);
      }
      if (formDataAreaFromLocalStorage.material_per_sq_ft) {
        // If there are additional materials, set them to the additionalMaterials state
        setMaterialPerSqft(formDataAreaFromLocalStorage.material_per_sq_ft);
      } else {
        // If no additional materials, initialize the additionalMaterials state as an empty array
        setMaterialPerSqft([]);
      }
      if (formDataAreaFromLocalStorage.material_deduct) {
        // If there are additional materials, set them to the additionalMaterials state
        setMaterialDeduct(formDataAreaFromLocalStorage.material_deduct);
      } else {
        // If no additional materials, initialize the additionalMaterials state as an empty array
        setMaterialDeduct([]);
      }
    } else {
      console.log("Data is not saved yet.");
    }
    //ended check saved data
    const fetchTradeData = async () => {
      try {
        const response = await fetch('/api/materials/divisions', {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${routes.__token}`,
          },
        });

        if (!response.ok) {
          // Handle error if the response is not OK (status code 2xx)
          // Example: console.log('Error fetching trade data');
          return;
        }

        const data = await response.json();
        setTrade(data);
      } catch (error) {
        // Handle error if there's an issue with the API call
        // Example: console.error('API call failed:', error);
      }
    };

    fetchTradeData();
    const fetchWallMaterials = async () => {
      try {
        const response = await fetch('/api/materials/' + projectId, {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${routes.__token}`,
          },
        });

        if (!response.ok) {
          // Handle error if the response is not OK (status code 2xx)
          // Example: console.log('Error fetching wall materials data');
          return;
        }

        const data = await response.json();
        setWallMaterials(data);
      } catch (error) {
        // Handle error if there's an issue with the API call
        // Example: console.error('API call failed:', error);
      }
    };
    fetchWallMaterials();
  }, []);
  return (
    createPortal(
      <Draggable>
        <div className="popup__count">
          <div className="modal-header">
            Area Takeoff
            <span className="close-btn" onClick={() => { setDisplay(false); }}>&times;</span>
          </div>
          <form className="modal-body" onSubmit={handleFormSubmit}>

            <label className="label">Wall Area</label>
            <input
              name="wall_total_area"
              type="number"
              step="0.0001"
              placeholder="wall_total_area"
              value={formDataArea.wall_total_area}
              onChange={handleInputChange}
            />
            <label className="label">Wall Perimeter</label>
            <input
              name="wall_total_perimeter"
              type="number"
              step="0.0001"
              placeholder="wall_total_perimeter"
              value={formDataArea.wall_total_perimeter}
              onChange={handleInputChange}
            />

            <label className="label">Takeoff Name</label>
            <input
              type="text"
              placeholder="Takeoff Name"
              name="name"
              value={formDataArea.name}
              onChange={handleInputChange}
            />

            <label className="label">Select Trade</label>
            <select
              name="trade"
              value={formDataArea.trade}
              onChange={handleInputChange}
            >
              <option value="" hidden="">
                -- Select Trade --
              </option>
              {trade.map((tradeItem) => (
                <option key={tradeItem.id} value={JSON.stringify(tradeItem)}>
                  {tradeItem.name}
                </option>
              ))}
            </select>
            <label className="label">Select Type</label>
            <select
              name="type"
              value={formDataArea.type}
              onChange={handleInputChange}
            >
              <option value="" hidden="">
                -- Select Type --
              </option>
              <option value="Flat">Flat</option>
              <option value="Pitch">Pitch</option>
            </select>
            <label className="label">Area Material</label>
            <select
              name="wall_material"
              value={formDataArea.wall_material}
              onChange={handleInputChange}
            >
              <option value="" hidden="">
                -- Select Material --
              </option>
              {wallMaterials.map((material) => (
                <option key={material.id} value={JSON.stringify(material)}>
                  {material.name}
                </option>
              ))}
            </select>
            <input
              name="Area_thickness"
              type="number"
              step="0.0001"
              placeholder="Area Thickness (In)"
              value={formDataArea.Area_thickness}
              onChange={handleInputChange}
            />
            <input
              name="Total_units"
              type="number"
              step="0.0001"
              placeholder="Total Units"
              readOnly
              value={formDataArea.Total_units}
              onChange={handleInputChange}
            />






            <label className="label text-center fw-bold">Rise and Drop</label>
            <div className="form-row">
              <select
                name="rise_drop"
                value={formDataArea.rise_drop}
                onChange={handleInputChange}
              >
                <option value="" hidden="">
                  -- Select Type --
                </option>
                <option value="Rise">Rise</option>
                <option value="Drop">Drop</option>
              </select>
              <input
                name="rise_drop_rise"
                type="number"
                step="0.0001"
                placeholder="Rise"
                value={formDataArea.rise_drop_rise}
                onChange={handleInputChange}
              />
              <input
                name="rise_drop_area_added"
                type="number"
                step="0.0001"
                placeholder="Area added"
                value={formDataArea.rise_drop_area_added}
                onChange={handleInputChange}
              />
              <input
                name="rise_drop_thickness"
                type="number"
                step="0.0001"
                placeholder="Thickness"
                value={formDataArea.rise_drop_thickness}
                onChange={handleInputChange}
              />
              <input
                name="rise_drop_total"
                type="number"
                step="0.0001"
                placeholder="Total Units"
                readOnly
                value={formDataArea.rise_drop_total}
                onChange={handleInputChange}
              />
              <input
                name="rise_drop_total_unit"
                type="number"
                step="0.0001"
                placeholder="Total Units"
                readOnly
                value={formDataArea.rise_drop_total_unit}
                onChange={handleInputChange}
              />
            </div>


            <label className="label text-center fw-bold">MAT/SUBGRADE/UNDERLAY</label>
            <div className="form-row">
              <select
                name="underlay_material"
                value={formDataArea.underlay_material}
                onChange={handleInputChange}
              >
                <option value="" hidden="">
                  -- Select Material --
                </option>
                {wallMaterials.map((material) => (
                  <option key={material.id} value={JSON.stringify(material)}>
                    {material.name}
                  </option>
                ))}
              </select>
              <input
                name="underlay_sq_ft"
                type="number"

                placeholder="Sq ft"
                value={formDataArea.underlay_sq_ft}
                onChange={handleInputChange}
              />
              <input
                name="underlay_thickness"
                type="number"
                step="0.0001"
                placeholder="Thickness (In)"
                value={formDataArea.underlay_thickness}
                onChange={handleInputChange}
              />
              <input
                name="underlay_total"
                type="number"
                step="0.0001"
                placeholder="Total Units"
                readOnly
                value={formDataArea.underlay_total}
                onChange={handleInputChange}
              />
              <input
                name="underlay_total_unit"
                type="number"
                step="0.0001"
                placeholder="Total Units"
                readOnly
                value={formDataArea.underlay_total_unit}
                onChange={handleInputChange}
              />
            </div>






            <label className="label text-center fw-bold">Additional Materials</label>
            {additionalMaterials.map((material, index) => (
              <div className="form-row my-2" key={index}>
                <select
                  name={`additional_material[${index}][material]`}
                  value={material.material}
                  onChange={handleInputChangeAdditionalMaterial}
                >
                  <option value="" hidden>
                    -- Select Material --
                  </option>
                  {wallMaterials.map((wallMaterial) => (
                    <option key={wallMaterial.id} value={JSON.stringify(wallMaterial)}>
                      {wallMaterial.name}
                    </option>
                  ))}
                </select>
                <input
                  type="number"
                  step="0.0001"
                  placeholder="Thickness (In)"
                  name={`additional_material[${index}][thickness]`}
                  value={material.thickness}
                  onChange={handleInputChangeAdditionalMaterial}
                />
                <input
                  type="number"
                  step="0.0001"
                  placeholder="quantity/ Per sq ft"
                  name={`additional_material[${index}][cubicFt]`}
                  value={material.cubicFt}
                  readOnly
                  onChange={handleInputChangeAdditionalMaterial}
                />
                <input
                  type="number"
                  step="0.0001"
                  placeholder="Total Units"
                  readOnly
                  name={`additional_material[${index}][totalUnits]`}
                  value={material.totalUnits}
                  onChange={handleInputChangeAdditionalMaterial}
                />
                {additionalMaterials.length > 1 && (
                  <button
                    className="btn btn-danger mr-2"
                    type="button"
                    onClick={() => handleDeleteClickAdditionalMaterial(index)}
                  >
                    <i className="fa-solid fa-trash" />
                  </button>
                )}
              </div>
            ))}
            <button
              className="btn btn-primary mr-2"
              type="button"
              onClick={handleAddClickAdditionalMaterial}
            >
              Add
            </button>





            {/* Add material per square foot */}


            <label className="label text-center fw-bold">MATERIAL PER SQ FT</label>
            <div className="form-row my-2" key={0}>
              <select
                name={`material_per_sq_ft[0]`}
                value={materialPerSqft[0]}
                onChange={handleInputChange}
              >
                <option value="" hidden>
                  -- Select Material --
                </option>
                {wallMaterials.map((material) => (
                  <option key={material.id} value={JSON.stringify(material)}>
                    {material.name}
                  </option>
                ))}
              </select>
              <input
                type="number"
                step="0.0001"
                placeholder="Quantity per sq ft"
                name="quantity_per_sq_ft[0]"
                value={quantityPerSqft[0]}
                onChange={handleInputChange}
              />
            </div>

            {materialPerSqft.slice(1).map((material, index) => (
              <div className="form-row my-2" key={index + 1}>
                <select
                  name={`material_per_sq_ft[${index + 1}]`}
                  value={material}
                  onChange={handleInputChange}
                >
                  <option value="" hidden>
                    -- Select Material --
                  </option>
                  {wallMaterials.map((material) => (
                    <option key={material.id} value={JSON.stringify(material)}>
                      {material.name}
                    </option>
                  ))}
                </select>
                <input
                  type="number"
                  step="0.0001"
                  placeholder="Quantity per sq ft"
                  name={`quantity_per_sq_ft[${index + 1}]`}
                  value={quantityPerSqft[index + 1]}
                  onChange={handleInputChange}
                />
                {materialPerSqft.length > 1 && (
                  <button className="btn btn-danger mr-2" type="button" onClick={handleDeleteClickPerSqft}>
                    <i className="fa-solid fa-trash" />
                  </button>
                )}
              </div>
            ))}

            <button className="btn btn-primary mr-2" type="button" onClick={handleAddClickPerSqft}>
              Add
            </button>






            {/* Add OR Deduct */}
            <label className="label text-center fw-bold">Add/Deduct</label>
            <div className="form-row my-2" key={0}>
              <select
                name={`material_deduct[0]`}
                value={materialDeduct[0]}
                onChange={handleInputChange}
              >
                <option value="" hidden>
                  -- Select Material --
                </option>
                {wallMaterials.map((material) => (
                  <option key={material.id} value={JSON.stringify(material)}>
                    {material.name}
                  </option>
                ))}
              </select>
              {/* <input
                          type="number"
                          step="0.0001"
                          placeholder="Thickness (In)"
                          name="additional_material[0][thickness]"
                          value={additionalMaterials[0]}
                          onChange={handleInputChange}
                          /> */}
            </div>
            {materialDeduct.slice(1).map((material, index) => (
              <div className="form-row my-2" key={index + 1}>
                <select
                  name={`material_deduct[${index + 1}]`}
                  value={material}
                  onChange={handleInputChange}
                >
                  <option value="" hidden>
                    -- Select Material --
                  </option>
                  {wallMaterials.map((material) => (
                    <option key={material.id} value={JSON.stringify(material)}>
                      {material.name}
                    </option>
                  ))}
                </select>
                {/* <input
                              name={`additional_material[${index + 1}]`}
                              value={additionalMaterials[index + 1]}
                              type="number"
                              step="0.0001"
                              placeholder="Thickness (In)"
                              /> */}
                {materialDeduct.length > 1 && (
                  <button className="btn btn-danger mr-2" type="button" onClick={handleDeleteClickDeduct}>
                    <i className="fa-solid fa-trash" />
                  </button>
                )}
              </div>
            ))}
            <button className="btn btn-primary mr-2" type="button" onClick={handleAddClickDeduct}>
              Add
            </button>


            {/* Start ADJUSTMENTS Materials */}
            <details
              className="tool__box"
              style={{ paddingInline: 0, marginBlock: "0.4rem" }}
            >
              <summary style={{ cursor: "pointer" }}>
                <div
                  className="tool__box-header"
                  style={{
                    border: "1px solid rgba(0, 0, 0, 0.2)",
                    borderRadius: 2,
                    pointerEvents: "none",
                  }}
                >
                  <label className="label text-center fw-bold">
                    ADJUSTMENTS Materials
                  </label>
                </div>
              </summary>

              {formDataArea &&
                formDataArea.adjustmentDatas &&
                formDataArea.adjustmentDatas.length > 0 &&
                formDataArea.adjustmentDatas.map((item, index) => (
                  <div className="form-row-line" key={index}>
                    {/* <div className="lineal-adjustment"> */}

                    <div className="lineal-additional ">
                      <div className="takeoff-additional">
                        <label className="additional_label">Description</label>
                        <input
                          className="border-blue"
                          type="text"
                          name={`adjustment_description[${index}]`}
                          value={item.adjustment_description}
                          onChange={handleChangeAdjustments}
                        />
                      </div>

                      <div className="takeoff-adjustment w-20">
                        <label className="adjustment_label">
                          Material Name
                        </label>
                        <select
                          className="border-blue"
                          name={`adjustment_material[${index}]`}
                          value={item.adjustment_material}
                          onChange={handleChangeAdjustments}
                        >
                          <option value="" hidden>
                            -- Select Material --
                          </option>
                          {wallMaterials.map((material) => (
                            <option
                              key={material.id}
                              value={JSON.stringify(material)}
                            >
                              {material.name}
                            </option>
                          ))}
                        </select>
                      </div>


                      <div className="takeoff-adjustment">
                        <label className="adjustment_label">Type</label>
                        <select
                          className="border-blue"
                          name={`adjustment_type[${index}]`}
                          value={item.adjustment_type}
                          onChange={handleChangeAdjustments}
                        >
                          {/* <option value="" hidden="">
                                    {" "}
                                    -- Select --{" "}
                                </option> */}
                          <option value="Add">Add</option>
                          <option value="Deduct">Deduct</option>
                        </select>
                      </div>
                      {/* </div> */}
                      <div className="takeoff-adjustment">
                        <label className="adjustment_label">Quantity
                        </label>
                        <input
                          type="number"
                          name={`adjustment_qty[${index}]`}
                          value={item.adjustment_qty}
                          onChange={handleChangeAdjustments}

                        />
                      </div>

                      <div className="takeoff-adjustment">
                        <label className="adjustment_label">Measure unit
                        </label>
                        <select
                          className="border-blue"
                          name={`adjustment_unit[${index}]`}
                          value={item.adjustment_unit}
                          readOnly
                          onChange={handleChangeAdjustments}
                        >
                          {/* <option value="" hidden="">
                                    {" "}
                                    -- Select --{" "}
                                </option> */}
                          <option value="1">sq ft - Area</option>
                          <option value="2">ft - length</option>
                          <option value="3">Piece - Quantity</option>

                        </select>
                      </div>



                      <div className="takeoff-additional">
                        <label className="additional_label">Measured Quantity</label>
                        <input
                          className="border-blue"
                          type="number"
                          name={`adjustment_measured_qty[${index}]`}
                          value={item.adjustment_measured_qty}
                          onChange={handleChangeAdjustments}
                          readOnly
                        />
                      </div>

                    </div>


                    {index === 0 && (
                      <button
                        className="btn-add-row mt-20px"
                        type="button"
                        onClick={() => handleAddClickAdjustment(index)}
                      >
                        Add
                      </button>
                    )}

                    {index > 0 && (
                      <button
                        className="btn-delete-row mt-20px"
                        type="button"
                        onClick={() => handleDeleteAdjustment(index)}
                      >
                        <i className="fa-solid fa-trash" />
                      </button>
                    )}
                  </div>
                ))}

            </details>




            <div name="totales_div" id="totales_div">tabla totales</div>





            <label className="label">Notes</label>
            <textarea
              name="note"
              rows={10}
              value={formDataArea.note}
              onChange={handleInputChange}
            />
            <button className="btn btn-secondary" type="submit">
              Start
            </button>
          </form>
        </div>
      </Draggable>, document.querySelector('#root'))
  )
}

export default AreaPopup;