import { useEffect, useState } from "react";
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { routes, projectId } from "../Constants"
import "../proposalpopup.css";

function GenerateProposalPopup({ setDisplay }) {
    // const [selectedId, setSelectedId] = useState('')
    const [fetchedData, setFetchedData] = useState(null);
    useEffect(() => {
        //ended check saved data
        const generateProposal = async (id) => {
            try {
              const response = await fetch(`/api/proposal/all/${id}`, {
                method: 'POST',
                headers: {
                  Authorization: `Bearer ${routes.__token}`,
                },
                
              });
          
              if (!response.ok) {
                // Handle error if the response is not OK (status code 2xx)
                // Example: console.log('Error generating proposal');
                return;
              }
          
              const data = await response.json();
              setFetchedData(data);
              // Assuming the response contains the proposal data
              // You can update the state or perform any other logic with the data here
            //   console.log('Generated proposal data:', data);
            } catch (error) {
              // Handle error if there's an issue with the API call
              // Example: console.error('API call failed:', error);
            }
          };
          generateProposal(projectId);
      }, []);
      function printProposal(dataId) {
        const printContents = document.getElementById(dataId).innerHTML;
        const originalContents = document.body.innerHTML;
      
        document.body.innerHTML = printContents;
        window.print();
      
        document.body.innerHTML = originalContents;
      }
    return (
        createPortal(
            <Draggable>
                <div className="popup__count">
                    <div className="modal-header">
                    Generated Proposal
                        <span className="close-btn" onClick={() => { setDisplay(false); }}>&times;</span>
                    </div>
                    <div className="modal-body">
                    <button className="align-end print-button" onClick={() => printProposal("printData")}>Print</button>
                    {fetchedData && ( 
                    <div id="printData">
                        {/* <img src="[URL to Company Logo]" alt="Company Logo" /> */}
                        <p>[Company logo]</p>
                        <h3 className="text-center margin-bottom">{fetchedData.company}</h3>
                        <p className="margin-bottom">Date: {fetchedData.todayDate}</p>

                        <p className="margin-bottom">Proposal submitted to: <b>[generalContractorName]</b></p>
                        <p className="margin-bottom">Work to be Performed at: <b>[projectName]</b></p>

                        <p className="margin-bottom">
                            We Hereby <b>{fetchedData.company}</b> propose to furnish all Materials, Equipment and perform all labor necessary for the completion of the <b>[tradesAutoGenerated]</b> and concrete work at the above project.
                        </p>

                        <p className="margin-bottom margin-left">Following list generated from all materials used in the project and display it below:</p>
                        <p><b>[Show Material here]</b></p>
                        {/* <ul>
                            {materialsList.map((material, index) => (
                            <li key={index}>{material}</li>
                            ))}
                        </ul> */}

                        <p>
                            General contractor shall provide at no cost to <b>{fetchedData.company}</b> Electric hook up and power consumption, Temporary light Sanitary Facilities, and water hook up.
                            Any alterations or deviations from the above specifications involving extra cost will be executed only upon written change orders and will become extra charge over and above the original estimate.
                            All agreements contingent upon strikes, accidents, or delays beyond our control. Owner is to carry fire and tornado insurance.
                        </p>
                        <p>
                            <b>EXCLUDES:</b> Winter conditions and requirements, Builders Risk insurance and <span className="text-red"><b>USER TO LIST ANY EXCLUSIONS</b></span>
                        </p>

                        <p className="margin-bottom">
                            All work is to be guaranteed and performed in accordance with the drawings and specifications submitted for the above project.
                            All work to be completed in a substantial and workmanlike manner, all for the sum of <b>$ [projectTotalCost]</b>
                        </p>
                        <div>
                            <p>Name: <b>{fetchedData.name}</b></p>
                            <p>Company: <b>{fetchedData.company}</b></p>
                            <p>Phone: <b>{fetchedData.phone}</b></p>
                            <p>Email: <b>{fetchedData.email}</b></p>
                            <p>Website: <b>[Your Website URL]</b></p>
                        </div>
                    </div>
                    )}
                    </div>
                </div>
            </Draggable>, document.querySelector('#root'))
    )
}

export default GenerateProposalPopup;