import { useEffect, useState } from "react";
import { projectId, routes } from "../Constants";
import { toast } from "react-toastify";
import useSignal from "../hooks/signals";
import axios from "axios";
import "../pages_css.css";

function FileUpload() {
  const signal = useSignal();
  const [uploadProgress, setUploadProgress] = useState(0);
  const [uploading, setUploading] = useState(false);

  //   useEffect(() => {
  //     // Create the progress bar element if it doesn't exist
  //     let progressDiv = document.querySelector('#uploadProgressContainer');

  //     if (uploading) {
  //       if (!progressDiv) {
  //         progressDiv = document.createElement('div');
  //         progressDiv.id = 'uploadProgressContainer';

  //         // Find the div with the className 'drawing-board__container'
  //         const drawingBoardContainer = document.querySelector('.drawing-board__container');

  //         // Insert the progressDiv inside the 'drawing-board__container' div
  //         if (drawingBoardContainer) {
  //           drawingBoardContainer.appendChild(progressDiv);
  //         } else {
  //           // If 'drawing-board__container' div is not found, insert it at the beginning of the body
  //           document.body.insertBefore(progressDiv, document.body.firstChild);
  //         }
  //       }

  //       // Update the contents of the progress bar
  //       progressDiv.innerHTML = `
  //         <div>
  //           <p id="uploadProgress">Uploading Files...</p>
  //           <progress id="progressBar" value=${uploadProgress} max="100" />
  //         </div>
  //       `;

  //       if (uploadProgress === 100) {
  //         setTimeout(() => {
  //           const newProgressDiv = document.createElement('div');
  //           newProgressDiv.id = 'uploadProgressContainer';
  //           const parent = progressDiv.parentNode;
  //           parent.insertBefore(newProgressDiv, progressDiv.nextSibling);
  //           parent.removeChild(progressDiv);
  //           newProgressDiv.innerHTML = `
  //             <div>
  //               <p id="uploadProcessing">Processing Files...</p>
  //               <div class="spinner"></div> <!-- Add a div for the spinner -->
  //             </div>
  //           `;
  //         }, 1); // Adjust the delay as needed
  //       }
  //     } else {
  //         const processingFile = document.querySelector('#uploadProgressContainer');
  //         if(processingFile){
  //             while (processingFile.firstChild) {
  //                 processingFile.removeChild(processingFile.firstChild);
  //               }
  //         }
  //     }
  //   }, [uploading, uploadProgress]);

  useEffect(() => {
    let loadingOverlay = document.getElementById("loadingOverlay");

    if (!loadingOverlay) {
      loadingOverlay = document.createElement("div");
      loadingOverlay.id = "loadingOverlay";
      document.body.appendChild(loadingOverlay);
    }

    if (uploading) {
      // Show loading overlay
      loadingOverlay.style.display = "inline-block";

      // Update overlay content based on progress
      if (uploadProgress < 100) {
        loadingOverlay.innerHTML = `
          <p>Uploading Files... ${uploadProgress}%</p>
          <progress value="${uploadProgress}" max="100"></progress>
        `;
      } else {
        loadingOverlay.innerHTML = `
          <p>Processing Files...</p>
          <div class="spinner"></div>
        `;
      }
    } else {
      // Hide loading overlay
      loadingOverlay.style.display = "none";
    }
  }, [uploading, uploadProgress]);

  const handleFileUpload = async (e) => {
    let files = e.target.files;
    let totalFile = files.length;
    let uploaded = 0;

    // document.body.classList.add('isLoading');
    setUploading(true); // Display loading indicator

    Object.keys(files).forEach(async (index) => {
      let formData = new FormData();
      formData.append("file", files[index]);
      formData.append("project_id", projectId);
      formData.append("path", files[index].name);

      try {
        const response = await axios.post(routes.upload, formData, {
          headers: {
            Authorization: `Bearer ${routes.__token}`,
            "Content-Type": "multipart/form-data",
          },
          onUploadProgress: (progressEvent) => {
            const progress = Math.round(
              (progressEvent.loaded * 100) / progressEvent.total
            );
            // console.log('check progress', progress);
            setUploadProgress(progress);
          },
        });

        if (response.status === 200) {
          toast.success("Files Uploaded Successfully!", { ...toastOptions });
        } else if (response.status === 500) {
          toast.error("Files are not Uploaded, Try again!", {
            ...toastOptions,
          });
        } else {
          toast.error("Files are not Uploaded, Try again!", {
            ...toastOptions,
          });
        }
      } catch (e) {
        toast.error("Something went wrong. Please, try again!", {
          ...toastOptions,
        });
      }

      uploaded++;

      if (uploaded === totalFile) {
        signal.emit("refresh");
        setUploading(false);
        // document.body.classList.remove('isLoading');
        // console.log('check upload progress', uploadProgress);
        setUploadProgress(0);
      } else {
        const processingFile = document.querySelector(
          "#uploadProgressContainer"
        );
        if (processingFile) {
          while (processingFile.firstChild) {
            processingFile.removeChild(processingFile.firstChild);
          }
        }
      }
    });
  };

  const toastOptions = {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: 0,
  };

  return (
    <div>
      <input
        type="file"
        accept=".pdf"
        onChange={handleFileUpload}
        id="fileUpload"
        className="hidden"
        multiple
      />
    </div>
  );
}

export default FileUpload;
