import React, { useEffect, useState } from 'react';
import Dexie from 'dexie';
import { db } from '../database/db';
import { toast} from "react-toastify";
import LineGeneratePopup from './LineGeneratePopup';

function ProjectMeasurement({ plans }) {
  const [relatedData, setRelatedData] = useState([]);
  const [openLineGeneratePopup, setOpenLineGeneratePopup] = useState(false);
  const [allGroupedData, setAllGroupedData] = useState([]); 
  const [allGroupedDataArea, setAllGroupedDataArea] = useState([]); 
  const [allGroupedDataPerimeter, setAllGroupedDataPerimeter] = useState([]); 
  const [allGroupedDataCounts, setAllGroupedDataCounts] = useState([]);

  useEffect(() => {
    // Check if 'plans' is valid
    if (!plans || !Array.isArray(plans) || plans.length === 0) {
      toast.warning('No Related Record Found!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
      });

      // Exit early from useEffect if plans is not valid
      return;
    }

    // Proceed with the rest of your code if plans is valid
    const firstPlan = plans[0];
    const splitPlan = firstPlan.split('/');
    const projects_folder = splitPlan[0];
    const project_folder_name = splitPlan[1];
    const project_directory = splitPlan[2];
    const partialPath = projects_folder + '/' + project_folder_name + '/' + project_directory;

    // Function to fetch related data based on the partialPath
    async function fetchRelatedData() {
      try {
        await db.open(); // Open the Dexie database

        // Retrieve all data from the table
        const allData = await db.drawings.toArray();

        // Filter the data based on a partial match of 'path' with 'partialPath'
        const filteredData = allData.filter(item => item.path.includes(partialPath));
        // console.log('filteredData', filteredData);
        groupLines(filteredData);
        groupAreas(filteredData);
        groupPerimeters(filteredData);
        groupCounts(filteredData);
        // Set the related data in state
        setRelatedData(filteredData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }

    // Call the function to fetch related data
    fetchRelatedData();
  }, [plans]);
  // if (!plans || !Array.isArray(plans) || plans.length === 0) {
  //   toast.warning('No Related Record Found!', {
  //         position: "top-right",
  //         autoClose: 5000,
  //         hideProgressBar: false,
  //         closeOnClick: true,
  //         pauseOnHover: true,
  //         draggable: true,
  //         progress: 0,
  //     });
  // }
  //get all record
  // for local website
  // const firstPlan = plans[0];
  // const splitPlan = firstPlan.split('/');
  // const projects_folder = splitPlan[0];
  // const project_directory = splitPlan[2];
  // const partialPath = projects_folder + '/' + project_folder_name;

  // for live website
  // const firstPlan = plans[0];
  // const splitPlan = firstPlan.split('/');
  // const projects_folder = splitPlan[0];
  // const project_folder_name = splitPlan[1];
  // const project_directory = splitPlan[2];
  // const partialPath = projects_folder + '/' + project_folder_name+'/'+project_directory;
  // useEffect(() => {
  //   // Function to fetch related data based on the partialPath
  //   async function fetchRelatedData() {
  //     try {
  //       await db.open(); // Open the Dexie database

  //       // Retrieve all data from the table
  //       const allData = await db.drawings.toArray();

  //       // Filter the data based on a partial match of 'path' with 'partialPath'
  //       const filteredData = allData.filter(item => item.path.includes(partialPath));
  //       // console.log('filteredData', filteredData);
  //       groupLines(filteredData);
  //       groupAreas(filteredData);
  //       groupPerimeters(filteredData);
  //       groupCounts(filteredData);
  //       // Set the related data in state
  //       setRelatedData(filteredData);
  //     } catch (error) {
  //       console.error('Error fetching data:', error);
  //     } 
  //   }

  //   // Call the function to fetch related data
  //   fetchRelatedData();
   
  // }, [partialPath]);

  const groupLines = (allLinesData)=>{
    const allGroupedData = [];
  allLinesData.forEach(data =>{
    const allRecords = data.lines || [];
    // console.log('allRecords', allRecords);
    if (allRecords.length === 0) {
      return;
    }
    const groupedData = {};
    // Process the records to group them by meta.name
    allRecords.forEach(item => {
      const name = item.meta.name;
      const length1 = parseFloat(item.data[item.data.length - 1][1]); // Parse as float
      const length2 = item.data[item.data.length - 1][2]; // Keep length2 as is

      // Check if a group for this meta.name already exists
      if (groupedData[name]) {
        groupedData[name].totalLength1 += length1;
        groupedData[name].lines.push({
          name: item.meta.name,
          data: item.data,
          length1: length1,
          length2: length2,
        });
        const dataWithoutTotal = item.data.slice(0, -1); // Remove the last item (total)
        groupedData[name].data.push(...dataWithoutTotal);
      } else {
        // Create a new group for this meta.name
        groupedData[name] = {
          totalLength1: length1,
          lines: [
            {
              name: item.meta.name,
              data: item.data,
              length1: length1,
              length2: length2,
            },
          ],
          data: item.data.slice(0, -1), // Remove the last item (total)
        };
      }
    });
    // console.log('checking grouped data for lines', groupedData);
    allGroupedData.push(groupedData);
    // console.log('groupedData', groupedData);
  });
  // console.log('allGroupedData', allGroupedData);
    // Set allGroupedData state here
    setAllGroupedData(allGroupedData);
    // Check if allGroupedData is set and open the popup if it is
    if (allGroupedData.length > 0) {
      setOpenLineGeneratePopup(true);
    }
  }

  //getting Area Data
  const groupAreas = (allAreasData)=>{
    const allGroupedDataArea = [];
  allAreasData.forEach(data =>{
    const allRecords = data.area || [];
    // console.log('allRecords', allRecords);
    if (allRecords.length === 0) {
      return;
    }
    const groupedDataArea = {};
    // Process the records to group them by meta.name
    allRecords.forEach(item => {
      const name = item.meta.name;
      const length1 = parseFloat(item.data[item.data.length - 2][1]); // Parse as float
      const length2 = item.data[item.data.length - 2][2]; // Keep length2 as is
      // Check if a group for this meta.name already exists
      if (groupedDataArea[name]) {
        groupedDataArea[name].totalLength1 += length1;
        groupedDataArea[name].area.push({
          name: item.meta.name,
          data: item.data,
          length1: length1,
          length2: length2,
        });
        const dataWithoutTotal = item.data.slice(0, -1); // Remove the last item (total)
        groupedDataArea[name].data.push(...dataWithoutTotal);
      } else {
        // Create a new group for this meta.name
        groupedDataArea[name] = {
          totalLength1: length1,
          area: [
            {
              name: item.meta.name,
              data: item.data,
              length1: length1,
              length2: length2,
            },
          ],
          data: item.data.slice(0, -1), // Remove the last item (total)
        };
      }
    });
    // console.log('checking grouped data for lines', groupedData);
    allGroupedDataArea.push(groupedDataArea);
    // console.log('groupedData', groupedData);
  });
  // console.log('allGroupedDataArea', allGroupedDataArea);
    // Set allGroupedData state here
    setAllGroupedDataArea(allGroupedDataArea);
    // Check if allGroupedData is set and open the popup if it is
    if (allGroupedDataArea.length > 0) {
      setOpenLineGeneratePopup(true);
    }
  }
  // End Area Data

  //getting Perimeter Data
  const groupPerimeters = (allPerimetersData)=>{
    const allGroupedDataPerimeter = [];
    allPerimetersData.forEach(data =>{
    const allRecords = data.perimeter || [];
    // console.log('allRecords', allRecords);
    if (allRecords.length === 0) {
      return;
    }
    const groupedDataPerimeter = {};
    // Process the records to group them by meta.name
    allRecords.forEach(item => {
      const name = item.meta.name;
      const length1 = parseFloat(item.data[item.data.length - 1][1]); // Parse as float
      const length2 = item.data[item.data.length - 1][2]; // Keep length2 as is

      // Check if a group for this meta.name already exists
      if (groupedDataPerimeter[name]) {
        groupedDataPerimeter[name].totalLength1 += length1;
        groupedDataPerimeter[name].perimeter.push({
          name: item.meta.name,
          data: item.data,
          length1: length1,
          length2: length2,
        });
        const dataWithoutTotal = item.data.slice(0, -1); // Remove the last item (total)
        groupedDataPerimeter[name].data.push(...dataWithoutTotal);
      } else {
        // Create a new group for this meta.name
        groupedDataPerimeter[name] = {
          totalLength1: length1,
          perimeter: [
            {
              name: item.meta.name,
              data: item.data,
              length1: length1,
              length2: length2,
            },
          ],
          data: item.data.slice(0, -1), // Remove the last item (total)
        };
      }
    });
    // console.log('checking grouped data for lines', groupedData);
    allGroupedDataPerimeter.push(groupedDataPerimeter);
    // console.log('groupedData', groupedData);
  });
  // console.log('allGroupedDataPerimeter', allGroupedDataPerimeter);
    // Set allGroupedData state here
    setAllGroupedDataPerimeter(allGroupedDataPerimeter);
    // Check if allGroupedData is set and open the popup if it is
    if (allGroupedDataPerimeter.length > 0) {
      setOpenLineGeneratePopup(true);
    }
  }
  // End Perimeters Data

  //getting Counts Data
  const groupCounts = (allCountsData)=>{
    const allGroupedDataCounts = [];
    allCountsData.forEach(data =>{
    const allRecords = data.counts || [];
    // console.log('allRecords', allRecords);
    if (allRecords.length === 0) {
      return;
    }
    const groupedDataCounts = {};
    // Process the records to group them by meta.name
    allRecords.forEach(item => {
      const name = item.meta.name;
      const length1 = parseFloat(item.data[item.data.length - 1][1]); // Parse as float
      const length2 = item.data[item.data.length - 1][2]; // Keep length2 as is

      // Check if a group for this meta.name already exists
      if (groupedDataCounts[name]) {
        groupedDataCounts[name].totalLength1 += length1;
        groupedDataCounts[name].count.push({
          name: item.meta.name,
          data: item.data,
          length1: length1,
          length2: length2,
        });
        const dataWithoutTotal = item.data.slice(0, -1); // Remove the last item (total)
        groupedDataCounts[name].data.push(...dataWithoutTotal);
      } else {
        // Create a new group for this meta.name
        groupedDataCounts[name] = {
          totalLength1: length1,
          count: [
            {
              name: item.meta.name,
              data: item.data,
              length1: length1,
              length2: length2,
            },
          ],
          data: item.data.slice(0, -1), // Remove the last item (total)
        };
      }
    });
    // console.log('checking grouped data for lines', groupedData);
    allGroupedDataCounts.push(groupedDataCounts);
    // console.log('groupedData', groupedData);
  });
  // console.log('allGroupedDataCounts', allGroupedDataCounts);
    // Set allGroupedData state here
    setAllGroupedDataCounts(allGroupedDataCounts);
    // Check if allGroupedData is set and open the popup if it is
    if (allGroupedDataCounts.length > 0) {
      setOpenLineGeneratePopup(true);
    }
  }
  // End Counts Data
  // return groupedData;
  return (
    <div>
        {openLineGeneratePopup && (
            <LineGeneratePopup
                allGroupedData={allGroupedData}
                allGroupedDataArea={allGroupedDataArea}
                allGroupedDataPerimeter={allGroupedDataPerimeter}
                allGroupedDataCounts={allGroupedDataCounts}
                setDisplay={(isOpen) => setOpenLineGeneratePopup(isOpen)}
            />
          )}
    </div>
  );
}

export default ProjectMeasurement;