import { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { routes, projectId } from "../Constants";
import useSignal from "../hooks/signals";
import Details from "./Details";
// import ActiveTab from "./ActiveTab";
import FileView from "./FileView";
import "../Css/sidebar.css";
function SideBar() {
  const [plans, setPlans] = useState([]);
  const [copy, setCopy] = useState([]);
  const [search, setSearch] = useState("");
  const [refresh, setRefresh] = useState({});
  const [active, setActive] = useState([]);
  const [openFile, setOpenFile] = useState(null);
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const signal = useSignal();
  const [isSidebarOpen, setSidebarOpen] = useState(true);
  useEffect(() => {
    let refreshCallBack = () => {
      setRefresh({});
    };
    let activeCallBack = (e) => {
      let pathname = decodeURIComponent(e.detail.pathname);
      if (!active.includes(pathname)) {
        setActive([...active, pathname]);
      }
    };
    signal.listen("refresh", refreshCallBack);
    signal.listen("tab.active", activeCallBack);
    return () => {
      signal.unsubscribe("refresh", refreshCallBack);
      signal.unsubscribe("tab.active", activeCallBack);
    };
  }, [pathname]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    (async () => {
      let url = routes.plans + projectId;
      let response = await fetch(url, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${routes.__token}`,
        },
      });
      let data = await response.json();
      setPlans(data);
      if (data.length === 0) {
        setActive([]);
        setOpenFile(null);
        navigate("/");
      }
    })();
  }, [refresh]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    let result = plans.filter((value) =>
      value.toLocaleLowerCase().includes(search.toLocaleLowerCase())
    );
    setCopy(result);
  }, [search, plans]);
  const activeFile = pathname.split("/").pop();
  return (
    <div className={`sidebar__left ${isSidebarOpen ? "open" : "closed"}`}>
      <div className="toggle-left-sidebar">
        <Details />
        <div
          className="text-white angle-left-icon text-15 d-inline-block float-right toggle-icon"
          onClick={() => setSidebarOpen(!isSidebarOpen)}
        >
          {isSidebarOpen ? (
            <i className="fa fa-angle-left" aria-hidden="true"></i>
          ) : (
            <i className="fa fa-angle-right" aria-hidden="true"></i>
          )}
        </div>
      </div>
      ...
      <div className="left-bar-content">
        <div className="search__box">
          <input
            type="text"
            placeholder="Search"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
          <i className="fa-light fa-search search-icon"></i>
        </div>
        <FileView
          plans={copy}
          activeFiles={active}
          setActiveFiles={setActive}
          activeFile={activeFile}
          openFile={openFile}
        />
        {/* <ActiveTab plans={active} setActive={setActive} /> */}
      </div>
    </div>
  );
}
export default SideBar;
