import FileMenu from "./FileMenu";
import ScaleButton from "./ScaleButton";
import ScreenToggle from "./ScreenToggle";
import React, { useState, useEffect } from "react";
import useSignal from "../hooks/signals";
// import { useLocation } from "react-router-dom";
// import { useLiveQuery } from 'dexie-react-hooks'
// import { db } from "../database/db";
import CountPopup from "./CountPopup";
import Details from "./Details";
import LengthPopup from "./LengthPopup";
import LengthPopupDraw from "./LengthPopupDraw";
import LinePopup from "./LinePopup";
import AreaPopup from "./AreaPopup";
import PerimeterPopup from "./PerimeterPopup";
import OpeningPopup from "./OpeningPopup";
// import CanvasComponent from "./CanvasComponent";
import GenerateProposalPopup from "./GenerateProposalPopup";
import MaterialQoutePopup from "./MaterialQoutePopup";
import Dexie from "dexie";
import "../pages_css.css";
import "../Css/project-plan.css";
import CreateProjectPopup from "./CreateProjectPopup";
import ProjectButton from "./ProjectButton";
import LogoutButton from "./LogoutButton";
import ProjectDetails from "./ProjectDetails";
import { routes } from "../Constants";

function MenuBar() {
    const [active, setActive] = useState('')
    const [countDisplay, setCountDisplay] = useState(false);
    const [openLengthPopup, setOpenLengthPopup] = useState(false);
    const [openLengthPopupDraw, setOpenLengthPopupDraw] = useState(false);
    const [openAreaPopup, setOpenAreaPopup] = useState(false);
    const [openPerimeterPopup, setOpenPerimeterPopup] = useState(false);
    const [openCountPopup, setOpenCountPopup] = useState(false);
    const [openGenerateProposalPopup, setOpenGenerateProposalPopup] = useState(false);
    const [openMaterialQoutePopup, setOpenMaterialQoutePopup] = useState(false);
    // const [showCanvasComponent, setShowCanvasComponent] = useState(false);
    const [showHyperlinkModal, setShowHyperlinkModal] = useState(false);
    const [isCheckbox1Checked, setIsCheckbox1Checked] = useState(true);
    const [isCheckbox2Checked, setIsCheckbox2Checked] = useState(false);
    const [openCreateProject, setOpenCreateProject] = useState(false);
    const [lineDisplay, setLineDisplay] = useState(false)
    const signal = useSignal();
    const [openOpeningPopup, setOpenOpeningPopup] = useState(false);
    // const handleButtonClick = () => {
    //     setShowCanvasComponent(!showCanvasComponent);
    //   };
    const assetPath = routes.assetPath;
    var dB = new Dexie('takeoff');
    dB.version(1).stores({
        measurements: 'takeoff, name',
    });
    useEffect(() => {
        if (active !== '') {
            try {
                signal.emit('takeoff', { ...active })
            } catch (e) {
                console.log(e)
            }
        }
    }, [active, signal])
    useEffect(() => {
        // Set the initial state when the modal is opened
        if (showHyperlinkModal) {
            setIsCheckbox1Checked(true);
            setIsCheckbox2Checked(false);
        }
    }, [showHyperlinkModal]);
    const handleHyperlinkClick = () => {
        // console.log('function called 2nd time');
        setShowHyperlinkModal(true);
        // if(isCheckbox1Checked){
            // console.log('button is clicked');
            setActive({ type: 'drawHyperlink1' });
        // }
    };
    const handleCheckbox2Click = () => {
        setIsCheckbox2Checked((prevChecked) => !prevChecked);
        setActive({ type: 'drawHyperlink2' });
        setShowHyperlinkModal(false);
        setIsCheckbox1Checked(false);
        setIsCheckbox2Checked(false);
    };
    const handleLengthTakeoff = async ()=>{
        await dB.open();
        const dbData = await dB.measurements.toArray();
        if(dbData){
            await dB.measurements.clear();
        }
        const lineInput = window.prompt('Enter Length Measurement Name:', '');
        const lengthName = lineInput?lineInput:'Line Takeoff';
        dB.open();
        dB.measurements.add({name: lengthName, takeoff: 'length'});
        setActive({ type: 'length' });
    }
    const startLineMeasurement = () => {
        // if (!data?.scale.length) {
        //     startScale()
        //     return
        // }
        setLineDisplay(true)
    }
    const handleAreaTakeoff = async ()=>{
        await dB.open();
        const dbData = await dB.measurements.toArray();
        if(dbData){
            await dB.measurements.clear();
        }
        const lineInput = window.prompt('Enter Area Measurement Name:', '');
        const areaName = lineInput?lineInput:'Area Takeoff';
        dB.open();
        dB.measurements.add({name: areaName, takeoff: 'area'});
        setActive({ type: 'area' });
    }

    const handlePerimeterTakeoff = async () =>{
        await dB.open();
        const dbData = await dB.measurements.toArray();
        if(dbData){
            await dB.measurements.clear();
        }
        const lineInput = window.prompt('Enter Perimeter Measurement Name:', '');
        const perimeterName = lineInput?lineInput:'Perimeter Takeoff';
        dB.open();
        dB.measurements.add({name: perimeterName, takeoff: 'perimeter'});
        setActive({ type: 'perimeter' });
    }

    const handleCountTakeoff =async () => {
        await dB.open();
        const dbData = await dB.measurements.toArray();
        if(dbData){
            await dB.measurements.clear();
        }
        const lineInput = window.prompt('Enter Counts Measurement Name:', '');
        const countName = lineInput?lineInput:'Count Takeoff';
        dB.open();
        dB.measurements.add({name: countName, takeoff: 'count'});
        setActive({ type: 'count' });
    }
    const createProject = () => {
        setOpenCreateProject(!openCreateProject);
    };
    const handleOpeningTakeoff = async () => {
        // Esta funcin maneja la lgica para agregar los datos de Openings en la base de datos
        // Similar a como haces con Length, Area, Perimeter, etc.
        await dB.open();
        const dbData = await dB.measurements.toArray();
        if (dbData) {
            await dB.measurements.clear();
        }
        const lineInput = window.prompt('Enter Openings Measurement Name:', '');
        const openingName = lineInput ? lineInput : 'Opening Takeoff';
        dB.open();
        dB.measurements.add({ name: openingName, takeoff: 'opening' });
        setActive({ type: 'opening' });
    };

    
    return (
        <div className="top-menu header-bg">
            <ul className="tool-bar">
                <ProjectDetails />
                <FileMenu />
                <li data-tooltip="Rotate" className="dropdown w-65px b-right" tabIndex="0" onClick={() => signal.emit('rotate')}>
                    <div className="text-center text-white">
                        <i className="fa-light fa-rotate"></i>
                        {/* <img class="d-inline-block" src={assetPath +"images/rotate-icon.svg"} alt="rotate icon"/> */}
                        <span class="d-block text-12">
                        ROTATE
                        </span>
                    </div>
                </li>
                <li data-tooltip="Length Takeeoff" 
                // onClick={handleLengthTakeoff} 
                    className="dropdown w-65px b-right" tabIndex="0">
                    <div className="text-center text-white">
                        <i className="fa-light fa-arrows-left-right"></i>
                        <span class="d-block text-12">
                            LENGTH
                        </span>
                     </div>
                    <ul class="dropdown-menu">
                        <li>
                            {/* <label
                                className="custom-label"
                                onClick={startLineMeasurement}
                            >
                                Full Takeoff
                            </label> */}
                            <label 
                            class="custom-label"
                            onClick={() => setOpenLengthPopup(true)}>
                                Full Takeeoff
                            </label>
                        </li>
                        <li>
                            <label class="custom-label"
                           /*  onClick={handleLengthTakeoff} */ 
                           onClick={() => setOpenLengthPopupDraw(true)}
                           >
                                Only Measurement
                            </label>
                        </li>
                    </ul>
                </li>
                <li data-tooltip="Area Takeoff" 
                // onClick={handleAreaTakeoff} 
                    className="dropdown w-65px b-right" tabIndex="0">
                    <div className="text-center text-white">
                        <i className="fa-solid fa-square"></i>
                        <span class="d-block text-12">
                            AREA
                        </span>
                     </div>
                    <ul class="dropdown-menu">
                        <li>
                            <label class="custom-label"
                                onClick={() => setOpenAreaPopup(true)}>
                                Full Takeeoff
                            </label>
                        </li>
                        <li>
                            <label class="custom-label" 
                            onClick={handleAreaTakeoff}>
                                Only Measurement
                            </label>
                        </li>
                    </ul>
                </li>
                <li data-tooltip="Perimeter Takeoff"
                //  onClick={handlePerimeterTakeoff} 
                 className="dropdown w-80px b-right" tabIndex="0">
                     <div className="text-center text-white">
                        <i className="fa-light fa-square"></i>
                        <span class="d-block text-12">
                            PERIMETER
                        </span>
                     </div>
                    <ul class="dropdown-menu">
                        <li>
                            <label class="custom-label"
                                onClick={() => setOpenPerimeterPopup(true)}>
                                Full Takeeoff
                            </label>
                        </li>
                        <li>
                            <label class="custom-label"
                            onClick={handlePerimeterTakeoff}>
                                Only Measurement
                            </label>
                        </li>
                    </ul>
                </li>
                <li data-tooltip="Counts" 
                // onClick={handleCountTakeoff}
                    className="dropdown w-65px b-right" tabIndex="0">
                     <div className="text-center text-white">
                        <i className="fa-light fa-00"></i>
                        <span class="d-block text-12">
                            Counts
                        </span>
                     </div>
                    <ul class="dropdown-menu">
                        <li>
                            <label class="custom-label" 
                                onClick={() => setOpenCountPopup(true)}>
                                Full Takeeoff
                            </label>
                        </li>
                        <li>
                            <label class="custom-label"
                            onClick={handleCountTakeoff}>
                                Only Measurement
                            </label>
                        </li>
                    </ul>
                </li>
               
                <li data-tooltip="Annotations" className="dropdown w-100px b-right" tabIndex={0}>
                    <div className="text-center text-white">
                        <i className="fa-light fa-shapes" />
                        <span class="d-block text-12">
                            ANNOTATIONS
                        </span>
                     </div>
                    <ul className="dropdown-menu">
                        <li>
                        <label className="custom-label"
                            onClick={() => setActive({ type: 'drawEllipse' })}>
                            <i
                            className="fa-light fa-circle mr"
                            style={{ transform: "scaleX(1.2)" }}
                            />
                            Oval
                        </label>
                        </li>
                        <li>
                        <label className="custom-label"
                        onClick={() => setActive({ type: 'drawRectangle' })}>
                            <i className="fa-light fa-rectangle mr"/> 
                            Rectangle
                        </label>
                        </li>
                        <li>
                        <label className="custom-label"
                        onClick={() => setActive({ type: 'drawLine' })}>
                            <i className="fa-light fa-arrows-left-right mr" /> 
                            Line
                        </label>
                        </li>
                        <li>
                        <label className="custom-label" 
                        onClick={() => setActive({ type: 'drawText' })}>
                            <i className="fa-light fa-text mr" /> 
                            Text
                        </label>
                        </li>
                        <li>
                        <label className="custom-label"
                        // onClick={() => setActive({ type: 'drawHyperlink' })}
                        onClick={handleHyperlinkClick}>
                            <i className="fa-light fa-link mr" /> 
                            Hyperlink
                        </label>
                        </li>
                    </ul>
                </li>
                <li data-tooltip="Openings" className="dropdown w-65px b-right" tabIndex="0">
                    <div className="text-center text-white">
                        <i className="fa-light fa-window-maximize"></i> {/* Cambia el cono segn el tuyo */}
                        <span className="d-block text-12">OPENINGS</span>
                    </div>
                    <ul className="dropdown-menu">
                        <li>
                            <label className="custom-label" onClick={() => setOpenOpeningPopup(true)}>
                                Full Takeoff
                            </label>
                        </li>
                        <li>
                            <label className="custom-label" onClick={handleOpeningTakeoff}>
                                Only Measurement
                            </label>
                        </li>
                    </ul>
                </li>
                {/* <li data-tooltip="Generate Report" className="dropdown" tabIndex={0}>
                    <i className="fa-light fa-file-chart-column" />
                    <ul className="dropdown-menu">
                        <li>
                            <label className="custom-label" 
                               onClick={() => setOpenGenerateProposalPopup(true)}>
                                Generate Proposal
                            </label>
                        </li>
                        <li>
                            <label className="custom-label" 
                               onClick={() => setOpenMaterialQoutePopup(true)}>
                                Material Qoute
                            </label>
                        </li> */}
                        {/* <li>
                            <label className="custom-label"
                            onClick={() => setActive({ type: 'generateProposal' })}>
                            Generate Report
                            </label>
                        </li> */}
                    {/* </ul>
                </li> */}
                    {/* <li>
                    <button onClick={handleButtonClick}>
                        {showCanvasComponent ? "Hide Drawing" : "Show Drawing"}
                    </button>
                    </li> */}
                   
            </ul>
            <ul className="tool-bar ml-auto">
                {/* <Details/> */}
                <ScaleButton />
                 <li data-tooltip="Create Project" onClick={createProject} className="dropdown b-right w-120px" tabIndex="0">
                    <div className="text-center text-white w-120px">
                        <i className="fa-light fa-plus plus-nav-icon float-left"></i>
                         <span class="d-block text-15">
                            New Project
                        </span>
                     </div>
                </li>
                 <li data-tooltip="Print" className="dropdown b-right" tabIndex="0" onClick={() => window.print()}>
                     <div className="text-center text-white">
                        <i className="fa-light fa-print"></i>
                     </div>
                </li>
                <ProjectButton />
                <LogoutButton />
                <ScreenToggle />
                {countDisplay?<CountPopup setDisplay={setCountDisplay}/>:<></>}
            </ul>
            {/* {showCanvasComponent && <CanvasComponent />}  */}

            {openLengthPopup && (
                <LengthPopup
                setDisplay={(isOpen) => setOpenLengthPopup(isOpen)}
                />
            )}
            
             {openLengthPopupDraw && (
                <LengthPopupDraw
                setDisplay={(isOpen) => setOpenLengthPopupDraw(isOpen)}
                />
            )}
            {openAreaPopup && (
                <AreaPopup
                setDisplay={(isOpen) => setOpenAreaPopup(isOpen)}
                />
            )}
             {openPerimeterPopup && (
                <PerimeterPopup
                setDisplay={(isOpen) => setOpenPerimeterPopup(isOpen)}
                />
            )}
            {openCountPopup && (
                <CountPopup
                setDisplay={(isOpen) => setOpenCountPopup(isOpen)}
                />
            )}
             {openGenerateProposalPopup && (
                <GenerateProposalPopup
                setDisplay={(isOpen) => setOpenGenerateProposalPopup(isOpen)}
                />
            )}
             {openMaterialQoutePopup && (
                <MaterialQoutePopup
                setDisplay={(isOpen) => setOpenMaterialQoutePopup(isOpen)}
                />
            )}
             {/* Render the HyperlinkModal when showHyperlinkModal is true */}
                  {showHyperlinkModal && (
                     <div
                         className="modal-overlay top-left popup-style"
                     >
                         <div className="modal-content hyperlink-modal">
                             <button className="close-button" onClick={() => setShowHyperlinkModal(false)}>
                                 X
                             </button>
                             <h3>Add Hyperlink</h3>
                             <h4>Hint</h4>
                             <p>
                                Click to add hyperlink, open second file and click second checkbox and click on file
                             </p>
                                <div className="checkbox-style">
                                    <label>First File Select</label>
                                <input
                                type="checkbox"
                                checked={isCheckbox1Checked}
                                onChange={() => setIsCheckbox1Checked(!isCheckbox1Checked)}
                            />
                                </div>
                                <div className="checkbox-style">
                                <label>Second File Select</label>
                            <input
                                type="checkbox"
                                checked={isCheckbox2Checked}
                                onChange={() => setIsCheckbox2Checked(!isCheckbox2Checked)}
                                onClick={handleCheckbox2Click}
                            />
                        </div>
                        {/* <div className="checkbox-style">
                             <button className="popup-submit-btn" onClick={handleSubmit}>Add Hyperlink</button>
                             <button className="popup-cancel-btn"  onClick={() => setShowHyperlinkModal(false)}>Cancel</button>
                         </div> */}
                         </div>
                     </div>
                 )}
                 {/* Conditionally render the ProjectMeasurement component */}
            {/* {openCreateProject && <CreateProjectPopup />} */}
            {openCreateProject && (
                <CreateProjectPopup
                setDisplay={(isOpen) => setOpenCreateProject(isOpen)}
                />
            )}
             {/* {lineDisplay ? <LinePopup setDisplay={setLineDisplay} /> : <></>} */}
             {/* Popup para "Openings" */}
            {openOpeningPopup && (
                <OpeningPopup
                    setDisplay={(isOpen) => setOpenOpeningPopup(isOpen)}
                />
            )}
        </div>
    )
}
export default MenuBar;
