import useSignal from "../hooks/signals";
import { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import TakeoffTable from "./TakoffTable";
import { useLiveQuery } from "dexie-react-hooks";
import { db } from "../database/db";
import LineTable from "./LineTable";
import CountTable from "./CountTable";
import AreaTable from "./AreaTable";
import PerimeterTable from "./PerimeterTable";
import AnnotationLineTable from "./AnnotationLineTable";
import AnnotationRectangleTable from "./AnnotationRectangleTable";
import AnnotationOvalTable from "./AnnotationOvalTable";
import AnnotationTextTable from "./AnnotationTextTable";
import AnnotationHyperlinkTable from "./AnnotationHyperlinkTable";
import { routes } from "../Constants";
import { toast } from "react-toastify";
import "../Css/rightbar.css";
function RightBar() {
  const [details, setDetails] = useState(null);
  const { pathname } = useLocation();
  const signal = useSignal();
  const [isRightBarOpen, setRightBarOpen] = useState(true);
  const [activeTab, setActiveTab] = useState("takeoff");
  const [lineTemplate, setLineTemplate] = useState([]);

  const toggleRightBarState = () => {
    setRightBarOpen(!isRightBarOpen); // Toggle the right bar state
  };
  const handleTabClick = (tab) => {
    setActiveTab(tab);
  };
  const assetPath = routes.assetPath;
  let data = useLiveQuery(async () => {
    let drawingData = await db.drawings
      .where({
        path: pathname,
      })
      .first();

    return drawingData;
  }, [pathname]);
  useEffect(() => {
    let tempDataCallBack = (e) => {
      let { data } = e.detail;
      setDetails(data);
    };
    signal.listen("takeoff.temp", tempDataCallBack);

    return () => {
      setDetails(null);
      signal.unsubscribe("takeoff.temp", tempDataCallBack);
    };
  }, [pathname]); // eslint-disable-line react-hooks/exhaustive-deps
  useEffect(() => {
    (async () => {
      let url = routes.templates;
      let response = await fetch(url, {
        method: "GET",
        headers: {
          Authorization: `Bearer ${routes.__token}`,
        },
      });
      let data = await response.json();
      //   console.log("checking response data", data.template);
      setLineTemplate(data.template);
    })();
  }, []);
  const handleEditTemplate = (id) => {
    toast.info("Edit template is under work", { ...toastOptions });
  };
  const handleUseTemplate = (id) => {
    toast.info("Use template is under work", { ...toastOptions });
  };
  function truncateString(str, maxLength) {
    if (str.length > maxLength) {
      return str.slice(0, maxLength) + "...";
    }
    return str;
  }
  const toastOptions = {
    position: "top-right",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: 0,
  };
  //   console.log(template);
  return (
    <div className={`sidebar__right ${isRightBarOpen ? "open" : "closed"}`}>
      {/* <div className="right-btn-toggle">
            <button className="right-toggle-button" onClick={toggleRightBarState}>
                <i class="fa fa-angle-right" aria-hidden="true"></i>
                </button>
            </div> */}
      <div class="w-100 d-inline-block takeoff-template-tabs">
        <div className="tab d-inline-block">
          <button
            className="text-white angle-right-icon text-15 d-inline-block"
            onClick={toggleRightBarState}
          >
            {isRightBarOpen ? (
              <i className="fa fa-angle-right" aria-hidden="true"></i>
            ) : (
              <i className="fa fa-angle-left" aria-hidden="true"></i>
            )}
          </button>
          {/* <button className="tablinks active">
                        <img src="images/tekeoff-icon.svg" alt="w"/>
                        TAKEOFF</button>
                        <button className="tablinks">
                        <img src="images/grid-icon.svg" alt="pp"/>
                        TEMPLATE</button> */}
          <button
            className={`tablinks ${activeTab === "takeoff" ? "active" : ""}`}
            onClick={() => handleTabClick("takeoff")}
          >
            <img src={assetPath + "images/tekeoff-icon.svg"} alt="takeoff" />
            TAKEOFF
          </button>
          <button
            className={`tablinks ${activeTab === "template" ? "active" : ""}`}
            onClick={() => handleTabClick("template")}
          >
            <img src={assetPath + "images/grid-icon.svg"} alt="template" />
            TEMPLATE
          </button>
        </div>
      </div>
      {activeTab === "takeoff" && isRightBarOpen && (
        <div className="takeoff__tab">
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-ruler"></i> Takeoff Details
              </div>
            </summary>
            <div className="tool__box-body">
              {details ? <TakeoffTable data={details} /> : <></>}
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-grip-lines"></i> Length Takeoff
              </div>
            </summary>
            <div className="tool__box-body">
              <LineTable
                data={data}
                setDetails={setDetails}
                pathname={pathname}
              />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-circle"></i> Count Takeoff
              </div>
            </summary>
            <div className="tool__box-body">
              <CountTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-solid fa-square"></i> Area Takeoff
              </div>
            </summary>
            <div className="tool__box-body">
              <AreaTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-square"></i> Perimeter Takeoff
              </div>
            </summary>
            <div className="tool__box-body">
              <PerimeterTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-arrows-left-right mr" /> Annotation
                Line
              </div>
            </summary>
            <div className="tool__box-body">
              <AnnotationLineTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-rectangle mr" /> Annotation Rectangle
              </div>
            </summary>
            <div className="tool__box-body">
              <AnnotationRectangleTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i
                  className="fa-light fa-circle mr"
                  style={{ transform: "scaleX(1.2)" }}
                />
                Annotation Oval
              </div>
            </summary>
            <div className="tool__box-body">
              <AnnotationOvalTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-text mr" /> Annotation Text
              </div>
            </summary>
            <div className="tool__box-body">
              <AnnotationTextTable data={data} setDetails={setDetails} />
            </div>
          </details>
          <details className="tool__box" open>
            <summary>
              <div className="tool__box-header">
                <i className="fa-light fa-text mr" /> Annotation Hyperlink
              </div>
            </summary>
            <div className="tool__box-body">
              <AnnotationHyperlinkTable data={data} setDetails={setDetails} />
            </div>
          </details>
        </div>
      )}
      {activeTab === "template" && isRightBarOpen && (
        <div className="template__tab">
          <details
            className="tool__box"
            open={isRightBarOpen && activeTab === "template"}
          >
            <summary>Line Template</summary>
            {lineTemplate.length > 0 ? (
              <table>
                <thead>
                  <tr>
                    <th>Name</th>
                    <th>Trade</th>
                    <th></th>
                    {/* Add more table headers as needed */}
                  </tr>
                </thead>
                <tbody>
                  {lineTemplate.map((item) => (
                    <tr key={item.id}>
                      <td title={item.template_name}>
                        {truncateString(item.template_name, 10)}
                      </td>
                      <td title={item.trade_name}>
                        {truncateString(item.trade_name, 10)}
                      </td>
                      <td>
                        <button
                          className="btn temp-edit"
                          onClick={() => {
                            handleEditTemplate(item.id);
                          }}
                        >
                          <i className="fa-solid fa-edit"></i>
                        </button>
                        <button
                          className="btn temp-use"
                          onClick={() => {
                            handleUseTemplate(item.id);
                          }}
                        >
                          use
                        </button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <p>No templates available.</p>
            )}
          </details>
        </div>
      )}
    </div>
  );
}

export default RightBar;
