import React, { useState, useEffect } from "react";
import LineRowContext from './LineRowContext';
import { db } from "../database/db";
import { useLiveQuery } from "dexie-react-hooks";
import LengthPopup from "./LengthPopup";
import { projectId } from "../Constants";
import { routes } from "../Constants";
import "../pages_css.css";
import "../Css/rightbar.css";

function LineTable( { data, setDetails, pathname }) {
    const [openLengthPopup, setOpenLengthPopup] = useState(false);
    const [formData, setFormData] = useState();
    const [dataTakeoff, setDataTakeoff] = useState();
    const current_selected_length = (state, length,name,lineTemplate) => {
         //localStorage.setItem("current_selected_length",length); 
         //localStorage.setItem("takeoff_name",name); 
         //localStorage.setItem("lineTemplate",lineTemplate); 
         const data = {
                    length: length,
                    name: name,
                    lineTemplate: lineTemplate
                };
        setDataTakeoff(data);
        // console.log(lineTemplate,lineTemplate)
       
       //localStorage.setItem("lineTemplate",null); 
       //localStorage.setItem("current_selected_length",null); 
      // localStorage.setItem("takeoff_name",null); 
        
        ObtenData(projectId,name)
        
    }
    const [context, setContext] = useState(null)
    const handleContext = (e, index) => {
        e.preventDefault();
        setDetails(null)
        let { clientX, clientY } = e;
        setContext({x: clientX, y: clientY, index})
    }
    const Data = useLiveQuery(async () => {
        let drawingData = await db.drawings.where({
            path: pathname
        }).first();
        return drawingData;
    }, [pathname])
    useEffect(() => {
        let contextEvent = () => {
            setContext(null)
        }
        document.addEventListener('click', contextEvent);
        return () => {
            document.removeEventListener('click', contextEvent)
        }
    }, [])
    // Grouping lines
    //const allRecords = data?.lines || [];
   // console.log("data",data);
    const allRecords = data?.lines || [];
    const groupedData = {};
    // Process the records to group them by meta.name
    allRecords.forEach((item) => {

        console.log("item ine table",item);
        const name = item.meta.name;
        const length1 = parseFloat(item.data[item.data.length - 1][1]); // Parse as float
        const length2 = item.data[item.data.length - 1][2]; // Keep length2 as is
        
        // Check if a group for this meta.name already exists
        if (groupedData[name]) {
        groupedData[name].totalLength1 += length1;
        groupedData[name].lineTemplate =item.meta.lineTemplate;
        groupedData[name].lines.push({
            name: item.meta.name,
            lineTemplate: item.meta.lineTemplate,
            data: item.data,
            length1: length1,
            length2: length2,
        });
        const dataWithoutTotal = item.data.slice(0, -1); // Remove the last item (total)
        groupedData[name].data.push(...dataWithoutTotal);
        } else {
            // Create a new group for this meta.name
            groupedData[name] = {
            totalLength1: length1,
            lineTemplate: item.meta.lineTemplate,
            lines: [{
            name: item.meta.name,
            lineTemplate: item.meta.lineTemplate,
            data: item.data,
            length1: length1,
            length2: length2,
            }],

            data: item.data.slice(0, -1), // Remove the last item (total)
        };
        }
    });
    let previousRowIndex = null;
    const handleMouseInOut = async(e, index, isEntering) => {
        let newLines = [...Data?.lines];
        if (newLines[index]) {
            // Check if the record exists in the database
            const existingRecord = await db.drawings
                .where({ path: pathname })
                .first();
                if (existingRecord) {
                    if (isEntering) {
                        existingRecord.lines[index].meta.stroke = '#f5ed05';
                        if(previousRowIndex){
                            existingRecord.lines[previousRowIndex-1].meta.stroke = '#e22b88';
                        }
                    } 
                    if (!isEntering) {
                        existingRecord.lines[index].meta.stroke = '#e22b88';
                        previousRowIndex = index+1;
                    }
                    // Update the record in the database
                    await db.drawings
                        .where({ path: pathname })
                        .modify({ lines: existingRecord.lines });
                }
        }
     };

     const ObtenData = async (projectId,name) => {
        const dataToSend = {
         // template_name: name,
          trade_name: name,
         // formData: updatedFormData,
          projectId: projectId,
        };
        console.log("dataToSend");
        console.log(dataToSend);
        const response = await fetch(`/api/ObtenData`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${routes.__token}`,
          },
          body: JSON.stringify(dataToSend),
        }).catch(function (error) {                        // catch
          console.log('Request failed', error);
        });;
        const data = await response.json();
        if (data.status === 200) {
    

          setFormData(data.resultado);
          console.log("data.resultado obtenData");
          console.log(data.resultado);
          setOpenLengthPopup(true);


        }
        if (data.status === 400) {
          // HTTP status code 400
          //toast.error("calculation error !", { ...toastOptions });
          console.log(data.resultado);
          //setFormData(updatedFormData);
        }
        if (!response.ok) {
          throw new Error("Error proccesning");
        }
        //console.log("proccesing result", data);
      };



    // console.log('grouped and ungrouped data', groupedData);
    return (
       <>
         <table>
              <tbody>
                {groupedData && Object.keys(groupedData).map((groupName, index) => (
                <tr
                    key={index}
                    onContextMenu={(e) => handleContext(e, index)}
                    onClick={() =>
                    setDetails({
                        name: groupedData[groupName].lines[0].name,
                        data: groupedData[groupName].data,
                    })
                    }
                >
                <div
                 onMouseEnter={(e) => handleMouseInOut(e, index, true)}
                 onMouseLeave={(e) => handleMouseInOut(e, index, false)}
               >
                    <td className='w-15'>
                    <span
                        className="line-preview"
                        style={{
                            bordercolor:'red',
                        }}
                    ></span>
                    </td>
                    <td>{groupName}</td>
                    <td>{groupedData[groupName].totalLength1.toFixed(2)}</td> 
                    <td>{groupedData[groupName].lines[0].length2}</td> 
                    <td>{(groupedData[groupName].lineTemplate!=null) ? JSON.parse(groupedData[groupName].lineTemplate).template_name : 'None'}</td> 
                    <td><button 
                            class="buttton custom-label"
                            onClick={() => current_selected_length(true,groupedData[groupName].totalLength1.toFixed(2),groupName,groupedData[groupName].lines[0].lineTemplate)}>
                              Details
                            </button></td> 
                    </div>
                </tr>
                ))}
            </tbody>
        </table>
        {context?<LineRowContext details={context}/>:<></>}
        {openLengthPopup && (
                <LengthPopup
                setDisplay={(isOpen) => setOpenLengthPopup(isOpen)}
                Current_data={formData}
                takeoff={dataTakeoff}
                //length={(length) => setOpenLengthPopup(isOpen,length)}
                />
            )}
       </>
    )
}

export default LineTable;