import { useRef, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { routes } from "../Constants";
import useSignal from "../hooks/signals";
import { MapInteractionCSS } from "react-map-interaction";
import FileLoader from "./FileLoader";
import SVGBoard from "./SVGBoard";

function Drawing() {
  const containerRef = useRef();
  const { pathname } = useLocation();
  const [rotate, setRotate] = useState(0);
  const [loading, setLoading] = useState(false);
  const signal = useSignal();
  const [transform, setTransform] = useState({
    scale: 0.01,
    translation: {
      x: 12,
      y: 155.898,
    },
  });
  const MIN_SCALE = 0.01;
  const MAX_SCALE = 0.5;
  useEffect(() => {
    let callBack = () => {
      setRotate((a) => a + 90);
    };
    signal.listen("rotate", callBack);

    return () => {
      signal.unsubscribe("rotate", callBack);
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  useEffect(() => {
    try {
      signal.emit("tab.active", { pathname });
    } catch (e) {
      // console.log(e)
    }
    const container = containerRef.current;
    const canvas = document.createElement("canvas");
    const canvasContext = canvas.getContext("2d");
    const scaleFactor = 4;
    (async () => {
      setLoading(true);
      const svgUrl = routes.public + pathname;
      // Fetch the SVG content
      const response = await fetch(svgUrl);
      const svgText = await response.text();

      // Create a temporary DOM element to parse the SVG content
      const tempContainer = document.createElement("div");
      tempContainer.innerHTML = svgText;
      const svgElement = tempContainer.querySelector("svg");

      if (svgElement) {
        // Get SVG dimensions
        const svgWidth = svgElement.getAttribute("width");
        const svgHeight = svgElement.getAttribute("height");

        // Set canvas dimensions to match SVG dimensions
        canvas.width = parseInt(svgWidth, 10) * scaleFactor;
        canvas.height = parseInt(svgHeight, 10) * scaleFactor;

        // Fill the canvas with white background
        canvasContext.fillStyle = "white";
        canvasContext.fillRect(0, 0, canvas.width, canvas.height);

        var image = new Image();

        image.onload = function () {
          // drawImage(image, xStart, yStart, imageWidth, imageHeight)
          canvasContext.drawImage(image, 0, 0, canvas.width, canvas.height);
          setLoading(false);
          // Now you can use the 'canvas' element to display the SVG as an image.
          // For example, if you want to append the canvas to the container:
          container.appendChild(canvas);
        };

        image.src =
          "data:image/svg+xml;charset=utf-8," + encodeURIComponent(svgText);
      } else {
        // Handle case where no SVG element is found in the fetched content
        setLoading(false);
      }
    })();

    return () => {
      let canvasAll = document.querySelectorAll("canvas");
      canvasAll.forEach((c) => c.remove());
    };
  }, [pathname]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <>
      <div className={`drawing-board__container ${loading ? "hidden" : ""}`}>
        <MapInteractionCSS
          minScale={MIN_SCALE}
          maxScale={MAX_SCALE}
          value={transform}
          onChange={setTransform}
          showControls={true}
        >
          <div
            className="drawing-board"
            ref={containerRef}
            style={{ transform: `rotate(${rotate}deg)` }}
          >
            <SVGBoard transform={transform} />
          </div>
        </MapInteractionCSS>
      </div>
      {loading ? <FileLoader /> : <></>}
    </>
  );
}
export default Drawing;
